/**
 * Video Popup
 */
.video-popup-wrapper {
    text-align: center;
    border-radius: 5px;
    overflow: hidden;
}
.video-popup-wrapper--with_image{
	box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.2);
}
.video-popup-wrapper.video-popup-wrapper__left{
    text-align: left;
}
.video-popup-wrapper.video-popup-wrapper__right{
    text-align: right;
}
.video-popup-wrapper__left .video-popup__title,
.video-popup-wrapper__right .video-popup__title{
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0;
    margin-left: 14px;
}
.video-popup-wrapper__right .video-popup__title{
    margin-left: 0;
    margin-right: 14px;
}
.video-popup-wrapper__left .video-popup__link,
.video-popup-wrapper__right .video-popup__link{
    vertical-align: middle;
}
.video-popup__wrapper-link.with-img {
    position: relative;
    z-index: 1;
}
.video-popup__wrapper-link.with-img:before {
    content: "";
    display: block;
    background-color: #000000;
    opacity: 0;
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transition: opacity 0.3s;
}
.video-popup__wrapper-link.with-img:hover:before {
    opacity: 0.8;
}
.video-popup-wrapper img {
    position: relative;
    z-index: 1;
    width: 100%;
    display: block;
}
.video-popup__title {
    margin-bottom: 12px;
    font-size: 14px;
    font-family: inherit;
    line-height: inherit;
    font-weight: 500;
}
.video-popup__wrapper-link {
    position: relative;
    z-index: 1;
    display: block;
}
.video-popup__content {
    position: absolute;
    z-index: 3;
    top: 50%;
    text-align: center;
    transform: translateY(-50%);
    left: 0;
    right: 0;
}
.video-popup-wrapper--without_image .video-popup__content{
    position: static;
    transform: translateY(0);
}
.video-popup-wrapper--without_image.video-popup-wrapper{
    overflow: visible;
}
.video-popup-wrapper--without_image .video-popup__wrapper-link.with-img:hover:before{
    opacity: 0;
}
.video-popup__link {
    position: relative;
    z-index: 1;
    display: inline-block;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.video-popup__link svg {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    margin-top: -9px;
    margin-left: -5px;
    transition: transform 0.3s;
}
.video-popup__link:hover svg{
	transform: scale(1.3);
}
.video-popup__responsive-title {
    display: none;
}
.video-popup__responsive-title>* {
    color: inherit !important;
}
.video-popup-wrapper--icon_shadow .video-popup__link{
	box-shadow: 0 10px 30px 0 rgba(28, 30, 35, 0.2);
}
@media only screen and (max-width: 768px) {
   .video-popup__wrapper-link.with-img .video-popup__title {
        display: none;
    }
    .video-popup__responsive-title {
        display: block;
    }
}