/* Slider */
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
}
.slick-list:focus {
    outline: none;
}
.slick-list.dragging {
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
}
.slick-track:before,
.slick-track:after {
    display: table;
    content: '';
}
.slick-track:after {
    clear: both;
}
.slick-loading .slick-track {
    visibility: hidden;
}

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px;
    outline: none;
}
[dir='rtl'] .slick-slide {
    float: right;
}
.slick-slide.slick-loading img {
    display: none;
}
.slick-slide.dragging img {
    pointer-events: none;
}
.slick-initialized .slick-slide {
    display: block;
}
.slick-loading .slick-slide {
    visibility: hidden;
}
.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}
.slick-arrow{
    font-size: 0;
    background-color: transparent;
    border-color: #e5e8ef;
    padding: 0;
    width: 42px;
    height: 42px;
    text-align: center;
    line-height: 38px;
    margin-bottom: 0;
    position: absolute;
    top: calc(50% - 21px);
    left: -50px;
    &:before{
        content: "\f053";
        display: inline-block;
        font-family: FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-size: 12px;
        color: #3a405b;
        margin-right: 1px;
        transition: all 400ms;
    }
    &.slick-next{
        right: -50px;
        left: auto;
        &:before{
            content: "\f054";
            margin-right: -3px;
        }
    }
    &:hover:before{
        color: #ffffff;
    }
}
.slick-dotted .slick-arrow{
    top: calc(50% - 39px);
}
.wpd_module_carousel{
    .slick-arrow{
        top: calc(50% - 38px);
        left: -65px;
    }
    .slick-arrow.slick-next {
        right: -65px;
        left: auto;
    }
    .slick-dotted .slick-arrow{
        top: calc(50% - 74px);
    } 
}
// theme slick customize
.slickSlider__arrow{
    position: absolute;
    bottom: 6px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    font-size: 20px;
    z-index: 1;
    cursor: pointer;
    transform: translateX(-52px);
    transition: transform 400ms;
    .slickSlider:hover &{
        transform: translateX(0);
    }
    &:before{
        content: "\f104";
        display: inline-block;
        font: normal normal normal 14px/1 FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    &--prev{
        left: 12px;
        right: auto;
    }
    &--next{
        right: 12px;
        left: auto;
        transform: translateX(52px);
        &:before{
            content: "\f105";
        }
    }
}

ul.slick-dots{
    padding: 0 60px 16px;
    margin-top: 40px;
    list-style: none;
    bottom: 0;
    width: 100%;
    text-align: center;
    line-height: 0;
    li{
        display: inline-block;
        vertical-align: middle;
        border-color: inherit;
        button{
            font-size: 0;
            margin-bottom: 0;
            padding: 5px;
            line-height: 0;
            background-color: transparent;
            border: none;
            border-color: inherit;
            cursor: pointer;
            &:before{
                content: '';
                display: inline-block;
                width: 10px;
                height: 10px;
                border: 2px solid rgba(0,0,0,0.5);
                border-radius: 10px;
                transition: all 400ms;
            }
            &:hover{
                background-color: transparent;
                &:before{
                    border: 2px solid rgba(0,0,0,0.5);
                }
            }
        }
        &.slick-active{
            button{
                &:before{
                    border: 2px solid rgba(0,0,0,0);
                    background-color: rgba(0,0,0,0.5);
                }
            }
        }
    }
}
ul.slick-dots.custom_color{
    li button:before,
    li.slick-active button:before,
    li button:hover:before{
        border-color: inherit;
    }
    li.slick-active button:before{
        border-width: 5px;
    }

}

.slickSlider .slick-track a:after{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 130px;
    z-index: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.3) 100%);
    opacity: 0;
    transform: translateY(50%);
    transition: opacity 400ms, transform 400ms,;
}
.slickSlider:hover a:after{
    opacity: 1;
    transform: translateY(0);
}
