/**
 * Price Block
 */
.price_item_wrapper {
    background-color: #ffffff;
    padding: 0 30px;
    border: 1px solid #f0f2f7;
    border-radius: 6px;
}

.price_item {
    position: relative;
    z-index: 1;    
    .price_item_wrapper{
        transition: box-shadow 0.3s, border-color 0.3s;
    }
}
.price_item.most_popular .price_item_wrapper,
.price_item .price_item_wrapper:hover{
    box-shadow: 0 20px 40px 0 rgba(28, 30, 35, 0.2);
    border-color: transparent;
}
.price_item .item_cost_wrapper h3 {
    font-weight: 600;
    color: inherit;
}
.price_item .item_cost_wrapper {
    position: relative;
    z-index: 1;
    text-align: center;
}
.price_item .item_cost_wrapper h3 {
    font-size: 12px;
    line-height: 1.5;
    text-transform: uppercase;
}
.price_item .item_cost_wrapper .bg-color {
    width: 0;
    height: 0;
}
.price_item .item_cost_wrapper.with-image .bg-color:before {
    height: auto;
    width: auto;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    opacity: 0.9;
}
.price_item .item_cost_wrapper .bg-color:before {
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% + 2px);
    display: block;
    top: 0px;
    left: -1px;
    background-color: inherit;
}
.price_item .item_cost_wrapper.custom-font h3 {
    font-family: inherit;
}

.price_item-cost {
    font-size: 62px;
    line-height: 1;
    padding: 28px 0 25px 0;
}
.price_item_title {
    padding-top: 20px;
}
.price_item-cost .price_item_prefix {
    font-size: 0.258em;
    vertical-align: super;
    display: inline-block;
    font-weight: 600;
    padding-right: 5px;
}
.price_item-cost .price_item_suffix {
    font-size: 0.225em;
    font-weight: 600;
}
.price_item_body {
    padding-top: 20px;
    padding-bottom: 50px;
    padding-left: calc(13% + 30px);
    padding-right: calc(13% + 30px);
    margin-left: -31px;
    margin-right: -31px;
    margin-bottom: -1px;
    position: relative;
    z-index: 1;
    background-size: cover;
    background-position: center;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
.price_item_body.with-image{
    padding-top: 35px;
    padding-bottom: 35px;
}
.price_item_body.with-image:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: -1;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
.price_item_body.with-image{
    color: #ffffff;
}
.price_item .items_text ul {
    padding: 0;
}
.price_item .items_text{
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
}
.main_wrapper .price_item .items_text li {
    list-style: none;
    padding: 7px 0;
    font-weight: inherit;
}
.main_wrapper .price_item ul li:before {
    margin-right: 10px;
}
.main_wrapper .price_item ul.wpd_list_times li:before {
    color: #a5a6af;
}
.price_item_description {
    font-weight: 600;
    /* padding: 25px 0 0 0; */
}
.price_item .price_item_number{
    font-weight: 300;
}
.price_button {
    text-align: center;
}
.price_item .shortcode_button {
    margin-top: 30px;
    margin-bottom: 30px;
    border-radius: 35px;
    border-width: 2px !important;
    display: block;
}
.price_item .shortcode_button{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    max-width: 250px;
}
.price_item .shortcode_button:hover,
.price_item .shortcode_button.alt {
    background-color: transparent;
}
.price_item .shortcode_button.alt:hover {
    color: #ffffff;
}
@media only screen and (max-width: 992px) {
    .price_item{
        .price_item_wrapper{
            padding: 0 20px;
        }
        .price_item_body{
            padding-left: calc(13% + 20px);
            padding-right: calc(13% + 20px);
            margin-left: -21px;
            margin-right: -21px;
        }
        .price_item-cost{
            font-size: 45px;
        }
        .price_item-cost .price_item_prefix{
            font-size: 0.358em;
        }
        .price_item-cost .price_item_suffix{
            font-size: 0.325em;
        }
    }
}
@media only screen and (max-width: 768px) {
    .price_item{
        .price_item-cost{
            font-size: 60px;
        }
        .price_item-cost .price_item_prefix{
            font-size: 0.258em;
        }
        .price_item-cost .price_item_suffix{
            font-size: 0.225em;
        }
    }
}