/**
 * Icon Link
 */
.wpd_message_box{
    position: relative;
    padding: 18px 25px;
    border-radius: 5px;
}
.wpd_message_box.wpd_message_box-with-icon{
    padding-left: 50px;
}
.wpd_message_box.wpd_message_box-closable{
    padding-right: 50px;  
}
.wpd_message_box__icon,
.wpd_message_box > .wpd_message_box__close{
    position: absolute;
    top: 50%;
    left: 15px;
    line-height: 1em;
    margin-top: -0.5em;
}
.wpd_message_box__icon.fa{
	font-size: 21px;
}
.wpd_message_box > .wpd_message_box__close{
    left: auto;
    text-align: center;
    right: 10px;
    line-height: 2em;
    width: 2em;
    margin-top: -1em;
    cursor: pointer;
}