/**
 * Wrapper 404
 */
.wrapper_404 {
	padding-top: 80px;
	padding-bottom: 48px;
}
.wrapper_404 h1 {
	font-size: 33px;
	line-height: 35px;
}
.wrapper_404 .pp_container h1{
	font-size: 48px;
    line-height: 58px;
}
.wrapper_404 .pp_container h2{
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 0.5em;
    margin-top: 0.5em;
    font-weight: 600;
}
.wrapper_404 .pp_container .post-password-form label{
	display: block;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
}
.wrapper_404 .number_404 {
	font-size: 200px;
	line-height: 150px;
	font-weight: 900;
	margin-bottom: 40px;
	-webkit-text-fill-color: transparent;
	-webkit-background-clip: text;
    background-size: cover;
    display: inline-block;
}
.wrapper_404 .number_404 + h1 {
	margin-bottom: 17px;
}
.wrapper_404 .wpd_404_search {
	padding-top: 28px;
	text-align: center;
}
.wrapper_404 .wpd_404_search .search_form {
	max-width: 400px;
	width: 100%;
	display: inline-block;
	vertical-align: middle;
	position: relative;
	.search-field{
		margin-bottom: 25px;
	}
}
.wrapper_404 .wpd_404_search .search_form .search_text {
	padding-right: 65px;
	margin-bottom: 30px;
	padding-left: 28px;
}
.wrapper_404 .wpd_module_button a {
	border-radius: 30px;
	padding-left: 40px;
	padding-right: 40px;
	margin-bottom: 40px;
}
.wrapper_404 .wpd_module_button a:hover{
	background: #fff;
    border: 1px solid #e8e8e8;
}