/**
 * Countdown
 */
.wpd-countdown {
	display: table;
	table-layout: fixed;
	width: 100%;
	margin-bottom: 20px;
}
.countdown-row {
	display: table-row;
}
.countdown-section {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	font-size: 24px;
	white-space: normal;
	position: relative;
	z-index: 1;
}
.is-countdown .countdown-section {
	padding: 0.9em 0.3em;
}
.countdown_wrapper--left {
	text-align: left;
}
.countdown_wrapper--right {
	text-align: right;
}
.wpd_module_counter.counter-center .stat_count_wrapper {
	text-align: center;
}
.wpd-countdown--shadow {
	-webkit-box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.1);
	-moz-box-shadow:    0px 0px 24px 0px rgba(0, 0, 0, 0.1);
	box-shadow:         0px 0px 24px 0px rgba(0, 0, 0, 0.1);
}
.countdown-section:after {
	left: auto;
	right: -1px;
}
.countdown-section .countdown-amount {
	display: block;
	line-height: 1.2em;
	font-weight: 300;
	text-align: center;
}
.countdown-period {
	display: block;
	font-size: 24px;
	line-height: 1;
	font-weight: 900;
	margin-top: 14px;
}
.wpd-countdown--size_medium .countdown-section {
	font-size: 30px;
	padding: 0.9em 0.3em;
}
.wpd-countdown--size_large .countdown-section {
	font-size: 40px;
	padding: 0.7em 0.3em;
}
.wpd-countdown--size_e_large .countdown-section {
	font-size: 100px;
	padding: 0.3em 0.3em;
}
@media only screen and (max-width: 1200px) {
	.wpd-countdown--size_medium .countdown-section{
		font-size: 30px;
	}
	.wpd-countdown--size_large .countdown-section{
		font-size: 40px;
	}
	.wpd-countdown--size_e_large .countdown-section{
		font-size: 50px;
	}
}
@media only screen and (max-width: 992px) {
	.countdown-period,
	.wpd-countdown--size_medium .countdown-period,
	.wpd-countdown--size_large .countdown-period,
	.wpd-countdown--size_e_large .countdown-period{
		font-size: 14px;
	}
}
@media only screen and (max-width: 768px) {
	.wpd-countdown--size_medium .countdown-section{
		font-size: 22px;
	}
	.wpd-countdown--size_large .countdown-section{
		font-size: 26px;
	}
	.wpd-countdown--size_e_large .countdown-section{
		font-size: 30px;
	}
	.countdown-period,
	.wpd-countdown--size_medium .countdown-period,
	.wpd-countdown--size_large .countdown-period,
	.wpd-countdown--size_e_large .countdown-period{
		font-size: 12px;
	}
}