/**
 * Footer
 */
.main_footer{
	font-size: 1em;
	&:after,
	&:before{
		content: '';
		clear: both;
		display: table;
		height: 1px;
	}
	a{
		color: inherit;
	}
	&:empty{
		display: none;
	}
	.calendar_wrap table thead th{
		background: transparent;
	}
	.widget_product_categories ul li ul,
	.widget_nav_menu ul li ul,
	.widget_archive ul li ul,
	.widget_pages ul li ul,
	.widget_categories ul li ul,
	.widget_recent_entries ul li ul,
	.widget_meta ul li ul,
	.widget_recent_comments ul li ul{
		padding: 11px 0 0 0;
	}
}
.main_footer .widget_nav_menu ul li li{
	padding-left: 11px;
}
.main_footer_wrapper {
	padding:24px 0 26px 0;
}
.copyright p,
.pre_footer p{
	margin-bottom: 0;
}
.copyright a{
	color: inherit;
}
.main_footer h3.widget-title{
	font-size: 0.85714em;
	line-height: inherit;
	border-bottom: 0;
	margin-bottom: 0;
	padding-bottom: 20px;
	font-weight: 600;
}

.main_footer .copyright .widget-title{
	padding-bottom: 6px;
}

.pre_footer a{
	font-weight: 600;
}

.main_footer .copyright{
	font-weight: 400;
}

.main_footer{
	.widget_nav_menu{
		ul li{
			padding: 0 0 11px 0px;
			color: inherit;
			&:before{
				display: none;
			}
			a{
				color: inherit;
			}
		}		
	}
}

@media only screen and (max-width: 768px) {
	.pre_footer a {
		padding: 0 10px !important;
	}
	.copyright .row .span4,
	.copyright .row .span6 {
		width: 100%;
		text-align: center;
		padding-top: 10px;
	}
	.top_footer .row [class*=span] + [class*=span] {
	    margin-top: 35px;
	}
}