/**
 * Blog SCSS
 */

.wpd_blog_list{	
	.post,
	.page{
		.wpd_blog_list__post_wrapper{
			margin-bottom: 30px;
			padding: 25px 30px 20px;
			border: 1px solid #f1f2f7;
			border-radius: 5px;
			background-color: #fff;
			transition: box-shadow 400ms;
			&:hover{
				box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.2);
			}
		}		
		&.single_post {
			background: none;
			padding-left: 0;
			padding-right: 0;
			box-shadow: none;
			border: none;
			border-bottom: 1px #f1f2f7 solid;
			border-radius: 0;
			margin-bottom: 40px;
			&:hover{
			 	box-shadow: none;
			}
			& .wpd_blog_list__footer {
			  	margin-top: 50px;
			  	margin-bottom: 20px;
				& .wpd_blog_list_tag{
					margin-right: auto;
					margin-left: 12px;
				}
				& .wpd_blog_list__share {
				  	float: right;
					& > a {
					  	display: none;
					}
					& .share_wrap {
						position: relative;
					  	top: auto;
					  	right: auto;
					  	& ul li {
						  	visibility: visible !important;
						  	opacity: 1 !important;
						  	padding: 0;
						  	margin: 0 10px 10px 0;
						  	width: 34px;
						  	height: 34px;
						  	&:last-child {
							  	margin-right: 0;
							}
						  	& a span {
							  	width: 34px;
							  	height: 34px;
							  	line-height: 33px;
							  	border-radius: 50%;
							  	color: #fff;
								&.fa-twitter{
								   background: #55acee;
								}
								&.fa-facebook {
								  	background: #3b5998;
								}
								&.fa-pinterest {
								  	background: #d0202a;
								}
							  	&.fa-google-plus {
								  	background: #df4b38;
								}
							  	&.fa-linkedin {
								  	background: #0088b5;
								}
							  	&.fa-vk {
								  	background: #45668e;
								}
							  	&.fa-tumblr {
								  	background: #32506d;
								}
							  	&.fa-envelope {
								  	background: #daa520;
								}
							  	&.fa-reddit {
								  	background: #000000;
								}
							}
						}
					}
				}
				.wpd_blog_list_tag {
				  	padding: 5px 20px 5px 0;
				  	& a {
					  	display: inline-block;
					  	vertical-align: top;
					  	margin: 0 6px 10px 0;
					  	font-size: 0.846em;
					  	line-height: 1.818;
					  	padding: 2px 10px;
					  	border-radius: 4px;
					  	background: #f0f2f7;
					  	text-transform: uppercase;
					  	font-weight: 600;
					  	transition: all 300ms;
					  	&:hover {
						  	color: #fff;
						}
					}
				}
			}
		  	& .wpd_blog_list__media {
			  	margin: 0 0 20px 0;
			  	border-radius: 5px;
			}
		  	& .wpd_blog_list__meta {
			  	margin-bottom: 34px;
			  	font-weight: 600;
			  	display: flex;
		  	    align-items: center;
		  	    flex-wrap: wrap;
		  	    margin-left: -12px;
			}
		}
	}
	&__header{
		margin-bottom: 20px;
		.wpd_blog_list_wrapper.items2 &,
		.wpd_blog_list_wrapper.items3 &,
		.wpd_blog_list_wrapper.items4 &{
			margin-bottom: 16px;
		}
	}
	&__media{
		margin: -25px -31px 25px;
		border-top-left-radius: 5px;
		border-top-right-radius: 5px;
		position: relative;
		z-index: 1;
		overflow: hidden;
		.wpd_blog_list_wrapper.items2 &,
		.wpd_blog_list_wrapper.items3 &,
		.wpd_blog_list_wrapper.items4 &{
			margin-bottom: 21px;
		}
	}
	&__content{
		p{
			margin-bottom: 16px;
		  	a {
			  	text-decoration: underline;
			  	&:hover {
				  	text-decoration: none;
				}
			}
		}
	  	img {
		  	border-radius: 5px;
		}
		&:after{
			content: '';
			clear: both;
			display: table;
		}
	}
	&__footer{
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-left: -12px;
		font-size: 13px;
		font-weight: 600;
		margin-top: 20px;
		div:first-child:not(.wpd_blog_list__post_author){
			margin-left: auto;
		}
	}
	&__empty_right_space{
		width: 100%;
	}
	&__post_author{
		margin-left: 12px;
		margin-right: auto;
		white-space: nowrap;
	  	.avatar{
			width: 32px;
			max-width: none;
			border-radius: 32px;
			vertical-align: middle;
			margin-right: 13px;
			.wpd_blog_list_wrapper.items4 &{
		  		margin-right: 8px;
		  	}
		}
		span{
			vertical-align: middle;
		  	text-transform: uppercase;
		  	.wpd_blog_list_wrapper.items4 &{
		  		font-size: 12px;
		  	}
		}
	}
	&__post_author,
	&__post_likes,
	&__comments_link,
	&__date,
	&__share{
		display: table-cell;
		vertical-align: middle;
		margin-top: 2px;
    	margin-bottom: 2px;
		a{
			color: inherit;
		}
	}
	&__post_likes{
		white-space: nowrap;
		cursor: pointer;
		transition: all 400ms;
		padding-left: 12px;
		.wpd_post_likes{
			&__value,
			&__icon{
				display: inline-block;
    			vertical-align: baseline;
			}
			&__icon{
				padding: 0 2px;
			}
		}
		&.already_liked{
			cursor: default;
			.wpd_post_likes__icon:before{
				content: "\f004";
			}
			&:hover{
				color: inherit;
			}
		}
	}
	.wpd_post_comments__icon,
	.wpd_post_date__icon{
		padding: 0 2px;
	}
	&__comments_link,
	&__date{
		padding-left: 12px;
		white-space: nowrap;
		
	}
	&__content{
		h1,h2,h3, h4, h5, h6{
			margin-top: 12px;
			margin-bottom: 12px;
			&:first-child{
				margin-top: 0;
			}
		}
	}
	&__categories{
		margin-bottom: 15px;
		.category{
			display: inline-block;
			a{
				font-size: 11px;
			    font-weight: 400;
			    text-transform: uppercase;
			    padding: 0px 12px;
			    color: #fff;
			    border-radius: 24px;
			    margin: 2px 5px 5px 0;
			    display: inline-block;
	    		&:hover{
	    			color: #fff;
	    		}
			}
		}
		.wpd_blog_list__media &{
			position: absolute;
			top: 30px;
			left: 0;
			max-width: 100%;
			width: 100%;
			padding: 0 30px;
		}
	}
	&__share{
		position: relative;
		& > a{
			display: block;
			position: relative;
			transition: color 300ms;
			line-height: 30px;
			text-align: center;
			margin-left: 2px;
			&:before {
				font-family: 'FontAwesome';
				font-weight:normal;
				content:"\f1e0";
				line-height: 21px;
				padding: 0;
				opacity: 0.6;
				margin-right: 7px;
				margin-left: 7px;
			}
		}
		.share_wrap {
		    display: block;
		    position: absolute;
		    right: 100%;
		    top: 0;
		    font-size: 0;
		    line-height: 0;
		    z-index: 555;
		    box-shadow: none;
		    visibility: hidden;
		    ul {
				padding: 0;
				margin: 0;
				list-style: none;
				white-space: nowrap;
				li{
					margin: 0 2px;
				    text-align: center;
				    line-height: 25px;
				    width: 30px;
				    height: 30px;
				    padding: 0;
				    display: inline-block;
				    background-color: #ffffff;
				    vertical-align: middle;
				    opacity: 0 !important;
				    visibility: hidden;
				    transition: all 300ms;
				    &:before {
						display: none;
					}
				    &:nth-child(1){
					    transition-delay: 0s;
					}
					&:nth-child(2){
					    transition-delay: 0.1s;
					}
					&:nth-child(3){
					    transition-delay: 0.15s;
					}
					&:nth-child(4){
					    transition-delay: 0.2s;
					}
				}
			}
			a{
				display: block;
				span {
					display: block;
					line-height: 30px;
					width: 30px;
					font-size: 16px;
					border-radius: 30px;
					transition: box-shadow 300ms;
				}
				&:hover span{
					box-shadow: 0px 10px 25px 0px rgba(28, 30, 35, 0.2);
				}
				.fa-twitter{
					color: #1aa9e1;
				}
				.fa-facebook {
					color: #3b5a9a;
				}
				.fa-pinterest {
					color: #d0202a;
				}
				.fa-google-plus {
					color: #be4a39;
				}
				.fa-linkedin {
					color: #0088b5;
				}
				.fa-vk {
					color: #45668e;
				}
				.fa-tumblr {
					color: #32506d;
				}
				.fa-envelope {
					color: #daa520;
				}
				.fa-reddit {
					color: #000000;
				}
			}
		}
		&:hover > .share_wrap ul li {
			opacity: 1 !important;
			visibility: visible !important;
			&:nth-child(1){
			    transition-delay: 0.2s;
			}
			&:nth-child(2){
			    transition-delay: 0.15s;
			}
			&:nth-child(3){
			    transition-delay: 0.1s;
			}
			&:nth-child(4){
			    transition-delay: 0s;
			}
		}
	}
	.author_box {
	  	position: relative;
	  	padding: 0 0 32px 70px;
	  	margin-bottom: 40px;
	  	border-bottom: 1px #f1f2f7 solid;
	  	min-height: 90px;
		&__avatar {
			position: absolute;
		  	left: 2px;
		  	top: 0;
		  	width: 50px;
		  	img {
			  	border-radius: 50%;
			  	width: 100%;
			  	height: auto;
			}
		}
		&__name {
			padding-bottom: 1px;
		  	font-size: 1em;
		  	line-height: 1.33;
		  	text-transform: capitalize;
		}
		&__desc {
			padding-bottom: 0;
		}
	}
	.slick-dots{
		margin-top: 0;
	}
	.slick-arrow{
		top: calc(50% - 46px);
	}
	.slick-dotted .slick-arrow{
		top: calc(50% - 64px);
	}
}
.search_result_form,
.post-password-form {
	max-width: 600px;
	padding: 0 30px;
	margin-left: auto;
	margin-right: auto;
}
.post-password-form{
	max-width: none;
	padding: 0px;
	margin-left: 0;
}
.post-password-form label{
	display: inline-block;
    min-width: 300px;
    vertical-align: bottom;
    margin-bottom: 0;
}
.post-password-form input[type=submit]{
	display: inline-block;
    vertical-align: bottom;
}

.search_result_form .search_text,
.post-password-form input[type=password] {
	width: 100%;
}
.post-password-form input[type=password]{
	margin-bottom: 0;
}
.post-password-form input[type=submit]{
	margin-bottom: 0;
	margin-top: 10px;
	margin-left: 10px;
}
.search_result_form .search_text{
	margin-bottom: 20px
}
.post-password-form input[type=password]{
	margin-top: 5px;
}

.wpd_blog_list__media{
	.wpd_video__wrapper{
		position: relative;
	}
	.wpd_video_wrapper__iframe{
		width: 100%;
	    position: relative;
	    padding-bottom: 60%;
		iframe{
			position: absolute;
			width: 100%;
			height: 100%;
		}
	}
	.wpd_video_wrapper__thumb + .wpd_video_wrapper__iframe{
		position: absolute;
		padding-bottom: 0;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    display: none;
	    overflow: hidden;
	    opacity: 0;
	    transition: all .2s ease-in;	
	    &.play_video{
			display: block;
			opacity: 1;
		}
	}
	.wpd_video_wrapper__thumb:before{
		content: '';
		position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    background-color: rgba(0,0,0,0.4);
	    transition: all .2s ease-in;
	}
	.wpd_video_wrapper__thumb.play_video:before{
		background-color: rgba(0,0,0,0.8);
	}
	.wpd_video__play_button{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 60px;
		height: 60px;
		margin-top: -30px;
		margin-left: -30px;
		color: #fff;
		text-align: center;
		font-size: 20px;
		border: 2px solid #fff;
		border-radius: 60px;
		cursor: pointer;
	    .wpd_video__play_icon{
	    	line-height: 60px;
	    	margin-left: 4px;
	    }
	}
	.wpd_audio_wrapper__iframe{
		position: relative;
	    width: 100%;
	    padding-bottom: 60%;
	    .wpd_blog_list_wrapper--image_square &{
	    	padding-bottom: 100%;
	    }
	    .wpd_blog_list_wrapper--image_horizontal &{
	    	padding-bottom: 80%;
	    }
	    .wpd_blog_list_wrapper--image_vertical &{
	    	padding-bottom: 125%;
	    }
	    .wpd_blog_list_wrapper--image_4_3 &{
	    	padding-bottom: 75%;
	    }
		iframe{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	.wpd_qoute_wrapper__thumb,
	.wpd_link_wrapper__thumb{
		position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    background-size: cover;
    	background-position: center;
		&:before{
			content: '';
			position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 100%;
		    background-color: rgba(0,0,0,0.5);
		}
	}
	&.post_format_qoute,
	&.post_format_link{
		.wpd_blog_list__categories{
			position: relative;
			top: 0;
			margin-top: 30px;
			margin-bottom: 0;
		}
	} 	
	.wpd_qoute__wrapper,
	.wpd_link__wrapper{
		padding: 25px 30px 35px;
		position: relative;
		z-index: 1;
		color: #ffffff;
		font-size: 21px;
		line-height: 1.8;	
		.wpd_blog_list_wrapper.items3 &,
		.wpd_blog_list_wrapper.items4 &{
			font-size: 16px;
		}	
	}
	.wpd_link__wrapper{
		&:after{
			content: "\f0c1";
			font: normal normal normal 14px/1 FontAwesome;
			text-rendering: auto;
    		-webkit-font-smoothing: antialiased;
    		-moz-osx-font-smoothing: grayscale;
			position: absolute;
			bottom: 15px;
		    right: 30px;
		    font-size: 25px;
		    line-height: 1;
		    transform: rotate(90deg);
		}
		.wpd_link__text{
			a{
				color: inherit;
			}
		}
	}
	.wpd_qoute__wrapper{
		&:after{
			content: "”";
			position: absolute;
			bottom: 5px;
			right: 40px;
			font-size: 90px;
			line-height: 0;
		}		
		.wpd_qoute__text + .wpd_qoute__author_wrapper{
			margin-top: 15px;
		}
		.wpd_qoute__author_wrapper{
			display: inline-table;
			img,
			.wpd_qoute__author_name{
				display: table-cell;
				vertical-align: middle;
			}
			img{
				margin-right: 12px;
				border-radius: 34px;
			}
			.wpd_qoute__author_name{
				font-size: 13px;
			}
		}
	}
	.wpd_blog_list__media_qoute{
		display: inline-block;
		width: 100%;
		vertical-align: middle;
		white-space: normal;
	}
	&.post_format_qoute{
		white-space: nowrap;
		&:after{
			content: '';
			display: inline-block;
			position: relative;
			width: 0;
			vertical-align: middle;
		}
	}
	ul.slick-dots{
		position: absolute;
	    li{
	        button{
	            &:before{
	                border: 2px solid rgba(255,255,255,0.5);
	            }
	            &:hover{
	                &:before{
	                    border: 2px solid rgba(255,255,255,1);
	                }
	            }
	        }
	        &.slick-active{
	            button{
	                &:before{
	                    background-color: #ffffff;
	                }
	            }
	        }
	    }
	}
	.slickSlider__arrow{
		top: calc(50% - 20px) !important;
		&:before{
			color: inherit;
		}
		&:hover{
			background: transparent;
		}
	}
}

.wpd_blog_list_wrapper .per_column_wrap{
	article.post{
		width: 100%;
	}
}

.post-navigation {
  	padding-bottom: 43px;
	& .nav-links {
	  	display: table;
	  	width: 100%;
		& > div {
		  	display: table-cell;
		  	width: 50%;
			& span:before,
			& span:after {
			  	font-family:'FontAwesome';
  				content: '\f104';
			  	font-size: 16px;
			  	line-height: 24px;
			  	display: inline-block;
			  	vertical-align: top;
			}
			& span:after {
			  content: '\f105';
			}
			&.nav-previous {
			  	& span:before {
				  	padding-right: 10px;
				}
				& span:after {
					display: none;
				}
			}
		  	&.nav-next {
			  	text-align: right;
				& span:after {
				  padding-left: 10px;
				}
				& span:before {
				   display: none;
				}
			}
		}
	}
	& a {
		display: inline-block;
	  	vert-align: top;
	  	margin-bottom: 20px;
	  	padding: 9px 30px;
	  	border-radius: 21px;
	  	line-height: 24px;
	  	font-size: 0.786em;
	  	font-weight: normal;
	  	text-transform: uppercase;
	  	background: transparent;
	  	transition: all 300ms;
	  	background: #fff;
		box-shadow: 0 10px 20px rgba(58,64,91, 0.1);
		&:hover {
			color: #ffffff;
		}
	  	& * {
		  	font-size: inherit;
		  	line-height: inherit;
		  	color: inherit;
		}
		& span {
			display: inline-block;
			font-weight: 600;
		}
	}
}

/* Related Posts */
.featured_posts {
	margin-bottom: 35px;
  	.posts_wrapper {
	  	margin: -30px 0 0 -30px;
	}
  	article {
	  	width: calc(50% - 30px);
		display: inline-block;
		vertical-align: top;
	  	margin: 30px 0 0 30px;
	}
  	.items_per_line3 article {
	  	width: calc(33.333% - 30px);

	}
	& > h3 {
		font-weight: 900;
		margin-bottom: 38px;
	}
  	& .wpd_blog_list__media img {
	  	width: 100%;
	  	height: auto;
	}
}

/* Post Comments */
#comments {
	padding:0;
	margin: 0;
  	& #respond {
	  	padding-top: 64px;
	  	& > h3 {
		  	font-weight: 900;
		  	margin-bottom: 28px;
		}
	}
	& #respond form#commentform {
		margin-left: -10px;
		margin-right: -10px;
		& [class*="span"],
		& p {
			padding-right: 10px;
			padding-left: 10px;
			&.comment-notes,
			&.logged-in-as {
			  	display: none;
			}
		}
		& label {
			padding: 3px 20px 4px 20px;
			font-size: 0.857em;
			line-height: 2;
			text-transform: uppercase;
			font-weight: 600;
			margin-bottom: 0;
			color: #a1a6bb;
		}
	}
	& > h3 {
	  	font-weight: 900;
	  	margin-bottom: 28px;
	}
  	&:empty {
		height: 0;
		line-height: 0;
		font-size: 0;
	}
  	& ol.comment-list,
	& ol.comment-list li.comment > ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}
	& ol.comment-list {
	  	margin-top: -20px;
	}
  	& ol.comment-list li.comment {
		padding: 0;
		margin: 0;
		&:before,
		&:after {
		  	display: none;
		}
	}
	& .pingback .stand_comment .thiscommentbody{
		padding-left: 0;
	}
	& .stand_comment {
	  	padding: 30px 0 0 0;
	  	& .comment_content {
			border-bottom: 1px #f1f2f7 solid;
		  	padding-bottom: 14px;
		  	& p {
			  	margin-bottom: 15px;
			}
		}
	  	& .thiscommentbody {
			position: relative;
			padding-left: 70px;
		  	& .commentava {
			  	position: absolute;
			  	left: 0;
			  	top: 0;
			  	width: 50px;
			  	& img {
				  	width: 100%;
				  	height: auto;
				  	border-radius: 50%;
				}
			}
		  	& .comment_info {
			  	padding-bottom: 3px;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				-webkit-flex-direction: row;
				-ms-flex-direction: row;
				flex-direction: row;
				-webkit-flex-wrap: nowrap;
				-ms-flex-wrap: nowrap;
				flex-wrap: nowrap;
				-webkit-justify-content: flex-start;
				-ms-flex-pack: start;
				justify-content: flex-start;
				-webkit-align-content: flex-start;
				-ms-flex-line-pack: start;
				align-content: flex-start;
				-webkit-align-items: flex-start;
				-ms-flex-align: start;
				align-items: flex-start;
			  	& > * {
					-webkit-order: 0;
					-ms-flex-order: 0;
					order: 0;
					-webkit-flex: 0 1 auto;
					-ms-flex: 0 1 auto;
					flex: 0 1 auto;
					-webkit-align-self: auto;
					-ms-flex-item-align: auto;
					align-self: auto;
					&.comment_meta:before,
					&.comment-reply-link:before {
						font-family: 'FontAwesome';
						font-weight:normal;
						padding-right: 8px;
						font-size: 1.15em;
					}
					&.comment_meta {
					  	flex-grow: 1;
					  	font-size: 0.857em;
					  	font-weight: normal;
					  	&:before {
							content:"\f017";
						}
					}
					&.comment_author_says {
						margin-right: 20px;
					  	font-weight: 600;
					  	& a {
						  	color: inherit;
						  	font-weight: 600;
						}
					}
				  	&.comment-reply-link {
					  	font-size: 0.857em;
					  	font-weight: normal;
						&:before {
						  	content:"\f112";
						  	font-size: 1em;
						}
					}
				}
			}
		}
	}
  	& ol.comment-list li ul li .stand_comment {
	  	margin-left: 70px;
	  	@media only screen and (max-width: 767px) {
	  		&{
	  			margin-left: 35px;
	  		}
		}
	}
  	& ol.comment-list li ul li ul li .stand_comment {
	  	margin-left: 140px;
	  	@media only screen and (max-width: 767px) {
	  		&{
	  			margin-left: 70px;
	  		}
		}
	}
  	& #reply-title > a {
	  	display: none;
	}
	& #reply-title > small {
		font-size: 100%;
	}
  	& .comment_info a,
	& #cancel-comment-reply-link {
		transition:color 0.3s;
		-webkit-transition:color 0.3s;
	} 
	.comment_content{
		& > h1,
		& > h2,
		& > h3,
		& > h4,
		& > h5,
		& > h6{
			margin-top: 12px;
			margin-bottom: 12px;
		}
	} 
}
/* post likes */
.wpd_list__post_likes{
	display: table-cell;
	vertical-align: middle;
	white-space: nowrap;
	cursor: pointer;
	transition: all 400ms;
	padding-left: 12px;
	.wpd_post_likes{
		&__value,
		&__icon{
			display: inline-block;
			vertical-align: baseline;
		}
		&__icon{
			padding: 0 2px;
		}
	}
	&.already_liked{
		cursor: default;
		.wpd_post_likes__icon:before{
			content: "\f004";
		}
		&:hover{
			color: inherit;
		}
	}
	a{
		color: inherit;
	}
}
/* blog module */
.items2,
.items3,
.items4{
	article.post{
		display: inline-block;
		vertical-align: top;
	}
}
.items2 article.post{
	width: calc(50% - 0.5px);
}
.items3 article.post{
	width: calc(33.3333% - 0.5px);
}
.items4 article.post{
