/**
 * Back to top
 */
#back_to_top {
    position: fixed;
    right: 30px;
    bottom: 30px;
    visibility: collapse;
    opacity: 0;
    transition: 300ms all;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    font-size: 0;
    background: rgba(0, 0, 0, 0.3);
}
#back_to_top:before {
    font-family: 'FontAwesome';
    content: '\f106';
    font-size: 22px;
    line-height: 38px;
    color: #fff;
    display: inline-block;
    vertical-align: middle;
    transition: 300ms all;
}
#back_to_top.show {
    visibility: visible;
    opacity: 1;
}
#back_to_top:hover {
    background: #fff;
    box-shadow: 0 10px 25px rgba(28,30,35,0.15);
}
#back_to_top:hover:before {
    color: #2d3844;
}
/* end back to top */