/**
 * Testimonials
 */
.module_testimonial .testimonials_item{
    list-style: none;
}
.testimonials_list{
    overflow: hidden;
}
.testimonials_list .testimonials_rotator{
    margin-left: -15px;
    margin-right: -15px;
}
.testimonials_list .testimonials_rotator > .testimonials_item,
.testimonials_list .testimonials_rotator .testimonials_item{
    float: left;
	box-sizing: border-box;
    padding-right: 15px;
    padding-left: 15px;
}
.testimonials_list.items1 .testimonials_rotator > .testimonials_item{
    width: 100%;
}
.testimonials_list.items2 .testimonials_rotator > .testimonials_item{
    width: 50%;
}
.testimonials_list.items3 .testimonials_rotator > .testimonials_item{
    width: 33.33%;
}
.testimonials_list.items4 .testimonials_rotator > .testimonials_item{
    width: 25%;
}
.testimonials-grid{
    margin: -40px 0 0 0;
    padding: 0;
}
.testimonials-grid > .testimonials_item{
    box-sizing: border-box;
    margin-top: 40px;
    padding: 0 15px;
    list-style: none;
}
.testimonials-grid.columns-2 .testimonials_item{
    float: left;
    width: 50%;
}
.testimonials-grid.columns-2 .testimonials_item:nth-child(2n + 1){
    clear: left;
}
.testimonials-grid.columns-3 .testimonials_item{
    float: left;
    width: 33.33%;
}
.testimonials-grid.columns-3 .testimonials_item:nth-child(3n + 1){
    clear: left;
}
.testimonials-grid.columns-4 .testimonials_item{
    float: left;
    width: 25%;
}
.testimonials-grid.columns-4 .testimonials_item:nth-child(4n + 1){
    clear: left;
}
.testimonials-grid:after{
    display: table;
    clear: both;
    content: '';
}
.module_testimonial.type1,
.module_testimonial.type2{
    text-align: center;
}
.module_testimonial.testimonials_align_center{
    text-align: center;
}
.module_testimonial.testimonials_align_left{
    text-align: left;
}
.module_testimonial.testimonials_align_right{
    text-align: right;
}
.module_testimonial .slick-dots{
    margin-top: 20px;
}
.module_testimonial.type1 h3,
.module_testimonial.type2 h3{
    position: relative;
    margin-bottom: 40px;
}
.module_testimonial .title_divider{
    display: block;
    width: 40px;
    height: 1px;
    margin: 0 auto;
    margin-bottom: 50px;
}
.module_testimonial.type1 .testimonials_photo{
    display: inline-block;
}
.module_testimonial.type1 .testimonials_photo img{
    width: 60px;
    height: 60px;
    -webkit-border-radius: 0;
            border-radius: 0;
}
.wpd_module_carousel .slick-slide .module_testimonial{
    margin-right: -15px;
    margin-left: -15px;
    padding-right: 0;
    padding-left: 0;
}

/* TYPE 2 */
.module_testimonial.type2 .nav_item_wrap{
    float: left;
    width: 130px;
    padding-right: 75px;
    padding-left: 75px;
}

.module_testimonial.type2 .testimonials-nav{
    max-width: 900px;
    margin-right: auto;
    margin-bottom: 40px;
    margin-left: auto;
    padding-top: 15px;
    text-align: center;
}
.module_testimonial.type2 .testimonials_content .testimonials_photo{
    display: none;
}
.module_testimonial.type2 .testimonials_photo{
    /*width: 90px;*/
    padding: 20px 0 27px 0;
    -webkit-transition: all 400ms;
            transition: all 400ms;
}
.module_testimonial.testimonials_align_left .testimonials_author_wrapper,
.module_testimonial.testimonials_align_right .testimonials_author_wrapper{
    display: table;
    width: 100%;
    margin-bottom: 20px;
    .testimonials_photo,
    .testimonials_title{
        display: table-cell;
        vertical-align: middle;
    }
    .testimonials_title{
        padding-left: 17px;
    }
}
.module_testimonial.testimonials_align_right .testimonials_author_wrapper{
    direction: rtl;
    .testimonials_title{
        padding-left: 0;
        padding-right: 17px;
    }
}
.module_testimonial.testimonials_align_center{
    .testimonials_author_wrapper{
        margin-bottom: 10px;
        .testimonials_photo{
            margin-bottom: 10px;
        }  
    }    
}
.testimonial_item_wrapper{
    .testimonials-text{
        line-height: 1.7;
    }  
}

.module_testimonial.type2 .testimonials_photo img{
    margin: 0 auto;
}
.module_testimonial.type2 .slick-active .testimonials_photo{
    width: 130px;
    padding: 0;
}
.module_testimonial.type2 .testimonials_photo img{
    max-width: 100%;
    height: auto;
}
.module_testimonial.type2 .testimonials-photo-wrapper{
    display: inline-block;
    max-width: 820px;
}
.module_testimonial.type2 .testimonials-photo-wrapper .slick-list{
    padding: 0 15px!important;
}
.module_testimonial.type2 .testimonials-photo-wrapper img{
    transition: all .5s;
    transform: scale(.7);
    opacity: .3;
}
.module_testimonial.type2 .testimonials-photo-wrapper .slick-current img{
    transform: scale(1);
    opacity: 1;
}
.module_testimonial.type2 .testimonials-photo-wrapper .slick-track{
    margin: 0 auto;
}
.testimonials_title{
    font-weight: 600;
    .testimonials_author_position{
        display: block;
        opacity: 0.65;
        font-style: italic;
        font-size: 1.15em;
    }
}
.module_testimonial .testimonials_item{
    padding-bottom: 30px;
}
.module_testimonial .testimonials-rate-wrap{
    margin: 15px 0 0 -5px;
}
.module_testimonial.type2 .testimonials-rate-wrap{
    font-size: .8em;
    margin: 15px 0 0 0;
}
.module_testimonial .testimonials-rate-wrap i{
    margin: 0 5px;
    color: #f2bc3b;
}
.module_testimonial .testimonials-rate-wrap i.grey{
    color: #d9d9d9;
}
.module_testimonial .testimonials_photo img.testimonials_round_img{
    border-radius: 50%;
}

@media only screen and (max-width: 768px){
    .module_testimonial.type2 .testimonials_photo{
        width: 70px !important;
    }
    .module_testimonial.type2 .testimonials-photo-wrapper{
        width: 210px;
        max-width: 100%;
    }
    .module_testimonial.type2 .testimonials-photo-wrapper img{
        width: 70px !important;
        height: 70px !important;
    }
}