/**
 * Boxed Layout
 */
.wpd_boxed_layout{
    width: 1300px;
    overflow: hidden;
    background: #fff;
    margin: 0 auto;
    box-shadow: 0 20px 40px 0 rgba(28,30,35,0.2);
    position: relative;
    z-index: 1;
    @media only screen and (max-width: 1200px) {
      &{
      	width: 992px;
      }
    }
    @media only screen and (max-width: 992px) {
      &{
      	width: 768px;
      }
    }
    @media only screen and (max-width: 768px) {
      &{
      	width: 100%;
      }
    }
}