/* ----------------------------------------------------------------------------
 * WordPress Core Styles
 * ------------------------------------------------------------------------- */

img {
    vertical-align:top;
    outline:none !important;
    max-width:100%;
    height:auto;
    user-select: none;
}

.alignnone {
    margin: 7px 30px 15px 0;
}

.a-center{
    text-align: center;
}
.a-left{
    text-align: left;
}
.a-right{
    text-align: right;
}

.aligncenter,
div.aligncenter {
    display: block;
    margin: 7px auto 5px auto;
}

.alignright {
    float: right;
    margin: 7px 0 15px 30px;
}

.alignleft {
    float: left;
    margin: 7px 30px 15px 0;
}

a img.alignright {
    float: right;
    margin: 7px 0 15px 30px;
}

a img.alignnone {
    margin: 7px 30px 15px 0;
}

a img.alignleft {
    float: left;
    margin: 7px 30px 15px 0;
}

a img.aligncenter {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

img[class*="wp-image-"] {
    border-radius: 5px;
}

.fleft {
    float:left;
}

.fright {
    float:right;
}
.dn {
    display:none;
}

.wp-caption {
    max-width: 100%;
    text-align: center;

}
.wp-caption-text{
    margin-top: 5px;
    font-style: italic;
}
.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption {
    max-width: 100%;
}
.hideme {
    display:none;
}
.wp-caption.alignnone {
    margin: 7px 30px 15px 0;
}

.wp-caption.alignleft {
    margin: 7px 30px 15px 0;
}

.wp-caption.alignright {
    margin: 7px 0 15px 30px;
}
.wp-caption.aligncenter{
    margin-left: auto;
    margin-right: auto;
}

.wp-caption img {
    border: 0 none;
    height: auto;
    margin: 0;
    max-width: 100%;
    padding: 0;
    width: auto;
}

.wp-caption .wp-caption-text,
.gallery-caption {
    margin: 0;
    padding: 0.5rem;
}
.textwidget img{
    /* margin: 1rem 0; */
}

.sticky {
}

.bypostauthor {
    background: inherit;
    padding:0;
}

/* ----------------------------------------------------------------------------
 * Recommended sizes
 * ------------------------------------------------------------------------- */
 .size-auto,
 .size-full,
 .size-large,
 .size-medium,
 .size-thumbnail {
    max-width: 100%;
    height: auto;
 }



/* Text meant only for screen readers. */
.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute !important;
        white-space: nowrap;
    height: 1px;
    width: 1px;
    overflow: hidden;
}

.screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: 14px;
    font-size: 0.875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000; /* Above WP toolbar. */
}

.custom_bg {
    position:fixed;
    left:0;
    top:0;
    width:100%;
    height:100%;
    display:block;
    z-index:-1;
    background-position:center;
}

.custom_bg.img_bg {
    background-size:cover;
}

#main_content p img.alignright,
#main_content p img.alignleft {
  clear: both;
}

.oswald_hidden_if_empty{
    display: none;
}

/* Gallery Standard */
.gallery {
    margin: -20px 0 29px -30px;
    &-item {
        display: inline-block;
        vertical-align: top;
        padding: 0;
        margin: 0;
        text-align: center;
    }
    &-icon {
        padding: 30px 0 0 30px;
    }
    &-columns-1 .gallery-item {
        width: 100%;
    }
    &-columns-2 .gallery-item {
        width: 50%;
    }
    &-columns-3 .gallery-item {
        width: 33.333%;
    }
    &-columns-4 .gallery-item {
        width: 25%;
    }
    &-columns-5 .gallery-item {
        width: 20%;
    }
    &-columns-6 .gallery-item {
        width: 16.666%;
    }
    &-columns-7 .gallery-item {
        width: 14.285%;
    }
    &-columns-8 .gallery-item {
        width: 12.5%;
    }
    &-columns-9 .gallery-item {
        width: 11.111%;
    }
}

@media only screen and (max-width: 768px) {
    .gallery {
        margin: -10px 0 0 0;
        &-icon {
            padding: 20px 0 0 0;
        }
    }
    .gallery-columns-1 .gallery-item,
    .gallery-columns-2 .gallery-item,
    .gallery-columns-3 .gallery-item,
    .gallery-columns-4 .gallery-item,
    .gallery-columns-5 .gallery-item,
    .gallery-columns-6 .gallery-item,
    .gallery-columns-7 .gallery-item,
    .gallery-columns-8 .gallery-item,
    .gallery-columns-9 .gallery-item {
        width: 100%;
    }
}