/**
 * Base SCSS
 */
h1,
h2,
h3,
h4,
h5,
h6{
	font-weight: 500;
}

h1 span, h1 a,
h2 span, h2 a,
h3 span, h3 a,
h4 span, h4 a,
h5 span, h5 a,
h6 span, h6 a{
	color: inherit;
	font-family: inherit;
	font-weight: inherit;
	font-size: inherit;
	line-height: inherit;
}

#main_content {
	& > h1,
	& > h2,
	& > h3,
	& > h4,
	& > h5,
	& > h6 {
		margin-top: 12px;
		margin-bottom: 12px;
	}
}

p, address {
	margin: 0 0 16px 0;
	padding: 0;
}

label,
legend{
	display:block;
	margin-bottom:5px;
	font-weight:700;
}

input[type="checkbox"],
input[type="radio"]{
	display:inline;
	vertical-align: middle;
	margin: 3px;
}
input[type="date"],
input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="url"],
select,
textarea {
	padding: 10px 20px 9px 20px;
	outline:none;
	margin:0 0 20px 0;
	width:100%;
	display:block;
	font-size:14px;
	line-height: 1.75;
  	font-weight: 400;
	border-width:1px;
	border-style:solid;
	border-color: #e3e8f0;
	background-color: #f0f2f7;
  	box-sizing:border-box;
	-webkit-appearance:none;
  	border-radius: 21px;
}
input[type="submit"],
button{
	color: #ffffff;
	display: inline-block;
	vertical-align: top;
	margin-bottom: 20px;
	font-size: 11px;
	font-weight: normal;
	line-height: 22px;
	padding: 7px 30px;
	outline:none;
	border-width: 2px;
	border-style: solid;
	cursor: pointer;
	text-transform: uppercase;
	transition: all 400ms;
	border-radius: 20px;
}

input[type="submit"]:hover,
button:hover{
	background: #fff;
    border-color: #e8e8e8;
}

input[type="checkbox"] {
    display: inline-block;
    vertical-align: middle;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    line-height: 0;
    outline: none;
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    position: relative;
    z-index: 1;
    -webkit-box-sizing: border-box;
    -ms-box-szing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border-radius: 4px;
    background: #fff;
    border: 1px solid #c4cad0;
    vertical-align: middle;
}

input[type="checkbox"]:before {
    content: '\2713';
    width: 18px;
    line-height: 18px;
    display: inline-block;
    text-align: center;
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    z-index: 1;
    opacity: 0;
    font-weight: 700;
    color: #ffffff;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

input[type=checkbox]:checked:before {
	opacity: 1;
}

textarea {
  	height: 130px;
  	resize: none;
}

/* Placeholder */
::-webkit-input-placeholder {
  opacity: 1 !important;
}
:-moz-placeholder { /* Firefox 18- */
  opacity: 1 !important;
}
::-moz-placeholder {  /* Firefox 19+ */
  opacity: 1 !important;
}
:-ms-input-placeholder {
  opacity: 1 !important;
}
/* WebKit browsers */
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  color: transparent !important;
}
/* Mozilla Firefox 4 to 18 */
input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  color: transparent !important;
}
/* Mozilla Firefox 19+ */
input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  color: transparent !important;
}
/* Internet Explorer 10+ */
input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  color: transparent !important;
}

form,
body,
iframe {
	margin:0;
	padding:0;
}

iframe,
embed{
	vertical-align: top;
	max-width:100%;
	display:block;
	padding:0;
	margin:0;
	border:0;
}

img,
table,
iframe {
	border:0;
}

table {
	border-collapse:collapse;
	border-spacing: 0;
	vertical-align: middle;
	width: 100%;
}
table td,
table th {
    padding: 5px 10px;
    text-align: center;
}
table tbody tr,
table thead tr,
table tfoot tr{
    border: 1px solid #f2f2f2;
}

table tbody th,
table tfoot th{
    border-right: 1px solid #f2f2f2;
}

p:empty {
	display:none;
	margin: 0;
}

pre {
    padding: 15px;
    white-space: pre-wrap;
    word-wrap: break-word;
    border: 1px #eee solid;
    border-radius: 5px;
    margin-bottom: 17px;
}

/* lists */
.site_wrapper .wpd_blog_list__content ul,
.comment_content ul,
#main_content > ul,
.wpb_text_column ul,
.textwidget ul,
.doc-sec-content ul{
	margin-bottom: 20px;    	
	> li,
	ul > li{	
		list-style: none;
		padding: 3px 0 3px 0;
		&:before{
			content: "\2022";
			font-family: monospace;
			display: inline-block;
			font-size: 18px;
			line-height: 7px;
			vertical-align: middle;
			margin-top: -4px;
			margin-right: 6px;
		}
	}
	ul,ol{
		margin-left: 15px;
		margin-bottom: 0;
	}
}
.site_wrapper .wpd_blog_list__content ol,
.comment_content ol,
#main_content > ol,
.wpb_text_column ol,
.textwidget ol,
.doc-sec-content ol{
	ol,
	&{
		margin:0 0 18px 0;
		padding:0;
		list-style: none; 
		counter-reset: li;	
	}
	li,
	ol li{
		counter-increment: li;
	}
	& > li,
	ol > li{
		position:relative;
		padding: 3px 0 3px 0;
		&:before{
			content: "."counter(li); 
			font-size: inherit;
		    display: inline-block; 
		    width: 1em; 
		    margin-left: 0;
		    margin-right: 0.5em; 
		    text-align: right; direction: rtl;
		    font-weight: 600;
		}
	}
	ul,ol{
		margin-left: 20px;
		margin-bottom: 0;
	}
}
/* end lists */

/* Blockquotes */
blockquote{
	position: relative;
	z-index: 1;
	padding: 30px 43px;
    border: 1px solid #f0f2f7;
	margin: 26px 0;
	font-size: 28px;
    line-height: 1.55555;
    quotes: "“" "”" "‘" "’";
    text-align: center;
    border-radius: 5px;
    &:before,
    &:after{
	    content: open-quote;
	    position: absolute;
	    display: inline-block;
	    top: 96px;
	    left: 10px;
	    color: inherit !important;
	    font-size: 3.1em;
	    line-height: 0;
	    width: 0;
	    letter-spacing: 0;
	    word-spacing: 0;
	    margin-top: -0.3em;
	    font-style: normal;
	}
	&:after{
		content: close-quote;
		left: auto;
		right: 40px;
	}
	p:last-child {
		margin-bottom: 0 !important;
	}
	cite{
		font-style: normal;
		font-size: 14px;
		text-transform: uppercase;
		font-weight: 600;
		letter-spacing: 1px;
		a{
			color: inherit;
		}
	}
}
/* End Blockquotes */

/* hr */
hr{
    border-style: solid;
    border: none;
    border-bottom: 1px solid #e0e1dc;
    margin: 30px 0;
}
/* end hr */

/* Default listing */
dl {
  margin-bottom: 20px;
  dt{
  	font-weight: 600;
  	text-decoration: underline;
  }
  dd {
	  margin: 0 0 20px 0;
	}
}
/* end Default listing */

/* Paging */
.site_wrapper {
	.pagerblock {
	  	text-align: center;
	  	padding: 0;
	  	margin: 0;
	  	list-style: none;
	  	& > li {
			display: inline-block;
		  	vertical-align: top;
			padding: 0;
			margin: 0 0 6px 0;
		  	& a {
			  	width: 38px;
			  	height: 38px;
			  	line-height: 38px;
			  	text-align: center;
			  	border-radius: 50%;
			  	background: transparent;
			  	display: block;
			  	font-weight: 600;
			  	transition: all 300ms;
				&.current {
					background: #ffffff;
					box-shadow: 0 20px 40px rgba(168,172,185, 0.62);
				  	cursor: default;
				}
			  	& i {
				  	font-size: 18px;
				  	display: block;
				  	line-height: inherit;
				}
			}
		}
	}
}

/* Alignment */
.text-left,
.align-left{
  	text-align: left;
}

.text-center,
.align-center{
  	text-align: center;
}

.text-right,
.align-right{
  	text-align: right;
}

/* Column Separators */
.row_has_column_separator .column_separator_parent {
	position: relative;
}
.row_has_column_separator .wpb_column > .vc_column-inner > .wpb_wrapper.column_separator_wrap:before {
  	position: absolute;
  	right: 0;
  	top: 0;
  	height: 100%;
  	width: 0;
  	border-width: 1px;
  	border-style: solid;
  	border-color: inherit;
  	content: '';
  	border-left: none;
  	border-top: none;
  	border-bottom: none;
}
.row_has_column_separator .wpb_column:last-child > .vc_column-inner > .wpb_wrapper.column_separator_wrap:before {
	display: none;
}
@media only screen and (max-width: 767px) {
	.row_has_column_separator .wpb_column > .vc_column-inner > .wpb_wrapper.column_separator_wrap:before {
	  	display: none;
	}
}

.main_wrapper{
	padding-top: 40px;
	box-sizing:border-box;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
}
@media only screen and (max-width: 767px) {
	.main_wrapper .container-full-width > .row{
		margin-left: 0;
		margin-right: 0;
		& > [class*=span]{
			padding-left: 0;
			padding-right: 0;
		}
	}
}
.wpd-page-title + .site_wrapper > .main_wrapper{
	padding-top: 0;
}
.main_wrapper.like_fullscreen_type {
	padding:0;
}
.site_wrapper {
	position:relative;
	z-index:0;
	min-height: 50vh;
}
.row.sidebar_left .content-container{
	float: right;
}
.vc_row-wrapper{
	position: relative;
	float: left;
	width: 100%;
}
.vc_row-wrapper.vc_row[data-vc-full-width]{
	overflow: visible;
}
.wpd_boxed_layout{
	.vc_row-wrapper.vc_row[data-vc-full-width]{
		left: 0 !important;
	    max-width: calc(100% + 30px) !important;
	    min-width: calc(100% + 30px) !important;
	    @media only screen and (max-width: 768px){
	    	&{
	    		max-width: 100% !important;
	    		min-width: 100% !important;
	    		margin-left: 0;
	    		margin-right: 0;
	    	}
	    }
	}
	.rev_slider_wrapper{
		left: 0 !important;
		max-width: 100% !important;
	}
	.sticky_header{
		width: 1300px;
		@media only screen and (max-width: 1200px) {
	      &{
	      	width: 992px;
	      }
	    }
	    @media only screen and (max-width: 992px) {
	      &{
	      	width: 768px;
	      }
	    }
	    @media only screen and (max-width: 768px) {
	      &{
	      	width: 100%;
	      }
	    }
	}
}

.vc_row-wrapper.row_rounded {
	border-radius: 5px;
	overflow: hidden;
}
.vc_row-wrapper.row_box_shadow{
	box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.2);
}
.box_shadow{
	box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.2);
}
.wpb_single_image.box_shadow{
	box-shadow: none;
	.vc_single_image-wrapper,
	figure > div{
		box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.2);
	}
}
.wpb_single_image{
	figure > div.vc_box_rounded{
		border-radius: 5px;
		overflow: hidden;
	}
}
.wpb_single_image{
	figure{
		display: inline-block;
		vertical-align: top;
		margin: 0;
		max-width: 100%;
	}
}
.vc_row-wrapper.row_higher_z_index{
	z-index: 1;
}
.wpd_triangle_shape{
	position: absolute;
	left: 0;
	bottom: 100%;
	width: 100%;
	overflow: hidden;
}
.wpd_triangle_shape div{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 200%;
	transform-origin: 104% 0;
}
.wpd_triangle_shape.wpd_triangle_shape--bottom_pos{
	bottom: auto;
	top: 100%;
	div{
		bottom: 0;
		top: auto;
		transform-origin: -9% 0;
	}
	&.right_angle div{
		transform-origin: 110% 0;
	}
}
/* .wpd_triangle_shape.left_angle div{
	transform-origin: 0 0;
} */
.wpd_triangle_shape.right_angle div{
	transform-origin: -9% 0;
}
.wpd_triangle_shape--box_shadow{
	div{
		box-shadow: 0px 0px 20px 0px rgba(28, 30, 35, 0.5) !important;
	}
}

/* Map */
#map-canvas {
  width: 100%;
  position: relative;
  padding-bottom: 51.3%;
  border-radius: 5px;
}
.vc_row-o-full-height #map-canvas {
  border-radius: 0 !important;
}
#map-canvas > div {
  width: 100%;
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}
#map-canvas img {
  max-width: none;
  max-height:none;
}
div.wpcf7-response-output{
	margin-top: 1em;
	border-radius: 5px !important;
}
@media all and (max-width: 767px){
  #map-canvas {
	height:auto !important;
  }
}
@media all and (min-width: 768px){
  .wpcf7-form .contacts_form label {
	padding-left: 10px;
	padding-right: 10px;
  }
  .wpcf7-form .contacts_form label .wpcf7-form-control-wrap {
	margin-left: -10px !important;
	width: 100%;
  }
}

/* Content Vertical Aligment */
.content_vertical_top > .vc_column-inner {
  justify-content: flex-start !important;
}
.content_vertical_middle > .vc_column-inner {
  justify-content: center !important;
}
.content_vertical_bottom > .vc_column-inner {
  justify-content: flex-end !important;
}

/* Revolution Slider */
.custom .tp-bullet,
.tparrows.custom {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  overflow: hidden;
  transition: all 300ms;
  background: rgba(255,255,255, 0);
  box-shadow: 0 0 0 2px rgba(255,255,255, 0.5) inset;
  display: block;
}
.custom .tp-bullet:before,
.custom .tp-bullet:after,
.tparrows.custom:before {
  display: none;
}
.custom .tp-bullet:hover,
.custom .tp-bullet.selected,
.tparrows.custom:hover {
  background: rgba(255,255,255, 1);
  box-shadow: 0 0 0 2px rgba(255,255,255, 1) inset;
}
.tparrows.custom {
  width: 42px;
  height: 42px;
}
.tparrows.custom:after {
  position: absolute;
  left: 16px;
  top: 8px;
  font-family: 'FontAwesome';
  font-weight:normal;
  font-size: 20px;
  color: #fff;
  transition: all 300ms;
  line-height: 24px;
  content:"\f104";
}
.tp-rightarrow.tparrows.custom:after {
  content:"\f105";
  left: 18px;
}

.wpd_flex {
    box-sizing: border-box;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.wpd-content-middle{
	box-sizing: border-box;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    align-items: center;
}