/**
 * Services Box SCSS
 */

.wpd_services_box {
    position: relative;
}

.wpd_services_box.to-left,
.wpd_services_box.to-right,
.wpd_services_box.to-top,
.wpd_services_box.to-bottom {
    -webkit-perspective: 1000px;
    perspective: 1000px;
}

.wpd_services_box_link {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    display: block;
    text-indent: -9999px;
}

.services_box-back,
.services_box-front {
    z-index: 1;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-perspective: inherit;
    perspective: inherit;
    -webkit-transition: transform .8s cubic-bezier(0.5, 0.2, 0.2, 0.8);
    -moz-transition: transform .8s cubic-bezier(0.5, 0.2, 0.2, 0.8);
    transition: transform .8s cubic-bezier(0.5, 0.2, 0.2, 0.8);
    -ms-transition:none;
    box-shadow: 0 30px 40px rgba(28,30,35, 0.2);
}

.wpd_services_img_bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
}

.wpd_services_img_bg:after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    content: '';
    background: -moz-linear-gradient(top, rgba(39,40,44,0) 0%, rgba(39,40,44,1) 100%);
    background: -webkit-linear-gradient(top, rgba(39,40,44,0) 0%,rgba(39,40,44,1) 100%);
    background: linear-gradient(to bottom, rgba(39,40,44,0) 0%,rgba(39,40,44,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#27282c', endColorstr='#27282c',GradientType=0 );
    opacity: 0.3;
}

.wpd_services_box_content {
    color: #fff;
    text-align: center;
    position: relative;
}

.wpd_services_box_content .fake_space {
    padding: 80px 25px;
}

.wpd_services_box_content .text_wrap {
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    font-weight: inherit;
    position: absolute;
    left: 25px;
    top: 50%;
    right: 25px;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-perspective: inherit;
    perspective: inherit;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: 2;
    -webkit-transform: translate3d(0, -50%, 70px) scale(.86);
    -moz-transform: translate3d(0, -50%, 70px) scale(.86);
    -o-transform: translate3d(0, -50%, 70px) scale(.86);
    transform: translate3d(0, -50%, 70px) scale(.86);
}

.index_number,
.wpd_services_box_title {
    position: absolute;
    left: 21px;
    bottom: 37px;
    right: 21px;
    z-index: 2;
    display: block;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-perspective: inherit;
    perspective: inherit;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translateZ(70px) scale(.86);
    -moz-transform: translateZ(70px) scale(.86);
    -o-transform: translateZ(70px) scale(.86);
    transform: translateZ(70px) scale(.86);
}

.wpd_services_box_title {
    z-index: 3;
    left: 30px;
    right: 30px;
    color: #ffffff;
    bottom: 40px;
    & .box_title {
        font-size: 24px;
        line-height: 27px;
        font-weight: 700;
    }
    & .box_subtitle {
        margin-bottom: 7px;
    }
}

.has_number .wpd_services_box_title {
    left: 62px;
}

.index_number {
    font-size: 72px;
    line-height: 65px;
    color: rgba(255,255,255, 0.2);
}

.wpd_services_box.to-left .services_box-front,
.wpd_services_box.to-right .services_box-front {
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
    transform: rotateY(0deg);
}

.wpd_services_box.to-left .services_box-back {
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.wpd_services_box.to-right .services_box-back {
    -webkit-transform: rotateY(-180deg);
    -moz-transform: rotateY(-180deg);
    -o-transform: rotateY(-180deg);
    transform: rotateY(-180deg);
}


.wpd_services_box.to-top .services_box-front,
.wpd_services_box.to-bottom .services_box-front {
    -webkit-transform: rotateX(0deg);
    -moz-transform: rotateX(0deg);
    -o-transform: rotateX(0deg);
    transform: rotateX(0deg);
}

.wpd_services_box.to-top .services_box-back {
    -webkit-transform: rotateX(-180deg);
    -moz-transform: rotateX(-180deg);
    -o-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
}

.wpd_services_box.to-bottom .services_box-back {
    -webkit-transform: rotateX(180deg);
    -moz-transform: rotateX(180deg);
    -o-transform: rotateX(180deg);
    transform: rotateX(180deg);
}

.wpd_services_box:hover .services_box-front,
.wpd_services_box:hover .services_box-back {
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transition: transform 1s cubic-bezier(0.3, 1, 0.6, 1);
    -moz-transition: transform 1s cubic-bezier(0.3, 1, 0.6, 1);
    transition: transform 1s cubic-bezier(0.3, 1, 0.6, 1);
    -ms-transition:none;
}

.wpd_services_box.to-left:hover .services_box-front {
    -webkit-transform: rotateY(-180deg);
    -moz-transform: rotateY(-180deg);
    -o-transform: rotateY(-180deg);
    transform: rotateY(-180deg);
}

.wpd_services_box.to-left:hover .services_box-back {
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
    transform: rotateY(0deg);
}

.wpd_services_box.to-right:hover .services_box-front {
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.wpd_services_box.to-right:hover .services_box-back {
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
    transform: rotateY(0deg);
}

.wpd_services_box.to-top:hover .services_box-front {
    -webkit-transform: rotateX(180deg);
    -moz-transform: rotateX(180deg);
    -o-transform: rotateX(180deg);
    transform: rotateX(180deg);
}

.wpd_services_box.to-top:hover .services_box-back {
    -webkit-transform: rotateX(0deg);
    -moz-transform: rotateX(0deg);
    -o-transform: rotateX(0deg);
    transform: rotateX(0deg);
}

.wpd_services_box.to-bottom:hover .services_box-front {
    -webkit-transform: rotateX(-180deg);
    -moz-transform: rotateX(-180deg);
    -o-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
}

.wpd_services_box.to-bottom:hover .services_box-back {
    -webkit-transform: rotateX(0deg);
    -moz-transform: rotateX(0deg);
    -o-transform: rotateX(0deg);
    transform: rotateX(0deg);
}

.services_box-back,
.services_box-front,
.wpd_services_box_link,
.wpd_services_img_bg:after {
    border-radius: 5px;
}

/* Disable Block Rotation */
.wpd_services_box {
    &.without_flip {
        overflow: hidden;
        .services_box-front:before{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,0.15);
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.5) 100%);
            border-radius: 5px;
        }
        & .services_box-back {
            background: none !important;
            box-shadow: none;
        }
        & .wpd_services_box_content .text_wrap,
        & .index_number,
        & .wpd_services_box_title {
            text-align: left;
            -webkit-transform: none;
            -moz-transform: none;
            -o-transform: none;
            transform: none;
            left: 40px;
            right: 40px;
            top: 100%;
            transition: top 500ms;
        }
        & .index_number,
        & .wpd_services_box_title {
            top: auto;
            transition: bottom 400ms;
        }
        & .has_number .wpd_services_box_title {
            left: 70px;
        }
        &:hover {
            .wpd_services_box_content .text_wrap {
                top: 50%;
            }
            .index_number,
            .wpd_services_box_title {
                bottom: calc(50% + 26px);
            }
        }
        & .wpd_services_img_bg:after {
            opacity: 0.4;
        }
    }
}