/**
 * Portfolio
 */
.wpdaddy_team_list{
	&__item{
		position: relative;
		z-index: 1;
		padding-bottom: 30px;
		&:hover .wpdaddy_team_list_social{
			opacity: 1;
		}
	}
	&__item.span3{
		width: 25%;
	}
	&__grid-sizer{
		height: 0 !important;
		min-height: 0 !important;
	}
	&__posts-container{
		.wpdaddy_team_list__grid-gutter{
			width: 0;
		}
	}
	&__filter{
		padding: 45px 0;
		margin-left: -18px;
		margin-right: -18px;
		font-weight: 600;
		text-transform: uppercase;
		a{
			display: inline-block;
			padding: 5px 18px;
		}
	}
	.wpdaddy_team_list_social{
	    font-size: 14px;
	    transition: opacity 0.4s ease;	
	    .wpdaddy_team_list_social__item,
	    a{
	    	display: inline-block;
	    	width: 28px;
	    	height: 28px;
	    	line-height: 28px;
	    	text-align: left;
	    	opacity: 0.4;
	    	font-size: 15px;
	    	&:hover{
	    		opacity: 1;
	    	}
	    }
	}
	&__image-holder{
		border-radius: 5px;
		overflow: hidden;
		transition: box-shadow 0.4s ease, opacity 0.4s ease;
		& > a{
			display: block;
			position: relative;
			z-index: 1;
			width: 100%;
		}
		img{
			width: 100%;
			max-width: none;
			border-radius: 5px;
			position: relative;
			z-index: 1;
			object-fit: cover;
			opacity: 0;
			transition: transform 0.4s ease, opacity 0.4s ease;			
		}
		.wpdaddy_team_list_social{
			position: absolute;
		    top: 15px;
		    left: 30px;
		    z-index: 2;
		    opacity: 0;
		    .wpdaddy_team_list_social__item,
		    a{
		    	display: block;
		    	text-align: center;
		    	opacity: 0.5;
		    	font-size: 14px;
		    }
		}
		&:hover{
			box-shadow: 0px 30px 40px 0px rgba(34, 35, 40, 0.3);
		}
	}
	&__image-placeholder{
		background-color: #e2e2e2;
		border-radius: 5px;
  		position: relative;
  		top: 0;
		width: 100%;
		opacity: 1;
	}
	&__item.image_loaded &__image-holder img{
		opacity: 1;
	}
	&__content{
		padding-top: 22px;
		padding-bottom: 25px;
	}
	&__title{
		font-size: 24px;
		line-height: 1.5;
		font-weight: 600;
	}
	&__image_placeholder{
		position: relative;
		z-index: 1;
		width: 100%;
		padding-bottom: 80%;
		background-color: #e2e2e2;
		border-radius: 5px;
		.wpdaddy_portfolio_list__item--image_square &{
			padding-bottom: 100%;
		}
	}
	&__position{
		font-size: 1.142857em;
		font-weight: 400;
	}
	&__item--content_on_image{
		padding-bottom: 45px;
		.wpdaddy_team_list__image-holder,
		.wpdaddy_team_list__content{
			width: calc(50% - 15px);
			display: inline-block;
			vertical-align: top;
			@media only screen and (min-width: 768px) and (max-width: 992px) {
				&{
					width: 100%;
				}
			}
			@media only screen and (max-width: 600px) {
				&{
					width: 100%;
				}
			}
		}
		.wpdaddy_team_list__content{
			padding-top: 15px;
			padding-bottom: 15px;
			margin-left: 30px;
			@media only screen and (min-width: 768px) and (max-width: 992px) {
				&{
					margin-left: 0;
				}
			}
			@media only screen and (max-width: 600px) {
				&{
					margin-left: 0;
				}
			}
		}
		.wpdaddy_team_list__description{
			padding-top: 12px;
		}
		.wpdaddy_team_list__view_more{
			text-transform: uppercase;
			font-size: 11px;
			font-weight: 600;
			padding-top: 10px;
			display: inline-block;
			span{
				line-height: 1;
			}
			&:after{
				content: "\f105";
				display: inline-block;
				font: normal normal normal 14px/1 FontAwesome;
				font-size: 1.36363636em;
				line-height: 0.6666666;
				padding-left: 7px;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
			&:hover{
				color: inherit !important;
			}
		}
	}
}
.wpdaddy_single_team_socials{
	margin: 0 -10px;
	&__item{
		display: inline-block;
		padding: 5px 10px;
		font-weight: 400;
		a{
			color: inherit;
			&:hover{
				color: inherit;
				span:after{
					opacity: 1;
				}
			}
			span{
				position: relative;
				&:after{
					content: '';
					position: absolute;
					top: 100%;
					left: 0;
					width: 100%;
					border-top: 1px solid;
					opacity: 0;
					transition: opacity 0.3s ease;	
				}
			}
			i{
				font-size: 16px;
			}
		}
	}
}
.wpdaddy_single_team_header{
	display: flex;
	padding-bottom: 25px;
	width: 100%;
	.wpdaddy_single_team_thumbnail__wrapper{
		margin-right: 60px;
		@media only screen and (max-width: 768px) {
			&{
				margin-right: 30px;
			}
		}
		@media only screen and (max-width: 600px) {
			&{
				width: 100%;
				margin-right: 0;
				margin-bottom: 25px;
			}
		}
	}
	.wpdaddy_team_title{
		margin-bottom: 22px;
		h2{
			font-weight: 600;
		}
	}
	@media only screen and (max-width: 600px) {
		&{
			flex-wrap: wrap;
		}
	}
}
.wpdaddy_single_team_info{
	&__item{
		display: flex;
		align-items: baseline;
		width: 100%;
		padding: 8px 0;
		h4{
			font-size: inherit;
			text-transform: uppercase;
			font-weight: 600;
			line-height: 1.5;
			padding-right: 10px;
		}
		a{
			color: inherit;
		}
	}
}