/**
 * progress_bar
 */
.vc_progress_bar .vc_single_bar{
    background: transparent;
    box-shadow: none;
    margin-bottom: 18px;
    &:before{
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 16px;
        left: 0;
        top: auto;
        bottom: 0;        
        border-radius: 16px;    
        background: #f0f2f7;    
    }
    .vc_bar{
        height: 16px;
        border-radius: 16px;
        top: auto;
        bottom: 0;
    }
    .vc_label{
        height: 3.5em;
        font-size: 1em;
        font-weight: 600;
        padding: 0;
    }
    .vc_label_units{
        float: right;
    }
}