/* ----------------------------------------------------------------------------
 * Header
 * ------------------------------------------------------------------------- */
 /** logo */
.logo_container{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    z-index: 1;
    &:before{
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }
    a{
        display: inline-block;
        vertical-align: middle;
        color: inherit;
        &:before{
            content: '';
            display: inline-block;
            vertical-align: middle;
            height: 100%;
        }
        h1{
            display: inline-block;
        }
    }
    img{
        vertical-align: middle;
        max-height: 100%;
        max-width: 100%;
        height: auto;
    }
    .site-title{
        color: inherit;
    }
    .sticky_logo{
        display: none;
    }
    .sticky_header & .sticky_logo{
        display: block;
    }
    .sticky_header &.sticky_logo_enable .site-title,
    .sticky_header &.sticky_logo_enable .default_logo,
    &.mobile_logo_enable .mobile_logo{
        display: none;
    }
}

@media only screen and (max-width: 1200px) {
    .logo_container.mobile_logo_enable .default_logo,
    .logo_container.mobile_logo_enable .site-title{
        display: none;
    }
    .logo_container.mobile_logo_enable .mobile_logo{
        display: block;
    }
}
/* end logo */
 /* menu style */
.main-menu,
.main-menu > ul,
.main-menu > div,
.main-menu > div>ul{
    position: relative;
    font-weight: 400;
    z-index: 1;
    margin: 0;
    padding: 0;
}
.main-menu > ul,
.main-menu > div,
.main-menu > div > ul{
    margin: 0 -17px;
}
.main-menu > ul.menu > li{
    padding: 10px 0;
}
.main-menu > ul:after, 
.main-menu > div > ul:after{
    content: '';
    clear: both;
    display: table;
}
.main-menu > ul > li,
.main-menu > div > ul > li{
    height: 100%;
}
.main-menu > ul > li > a,
.main-menu > div > ul > li > a{
    position: relative;
    display: block;
    z-index: 1;
}
.main-menu > ul > li > a,
.main-menu > div > ul > li > a{
    opacity: 1;
    color: inherit;
    padding: 0 9px;
    margin: 0 5px;
}
.main-menu > ul > li > a > span,
.main-menu > div > ul > li > a > span{
    letter-spacing:-0.4px;
}
.main-menu > ul > li > a:before,
.main-menu > div > ul > li > a:before{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    opacity: 0;
    border-top: 1px solid;
    transform: translateY(1px);
    -webkit-transform: translateY(1px);
    -moz-transform: translateY(1px);
    -ms-transform: translateY(1px);
    transition: all 300ms;
    -moz-transition: all 300ms;
    -ms-transition: all 300ms;
    -webkit-transition: all 300ms;
    will-change: transform;
}
.main-menu ul li{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    float: left;
    z-index: 1;
}
.main-menu ul li ul.sub-menu{
    display: block;
    position: absolute;
    left: 0;
    top: 100%;
    width: 250px;
    padding: 17px 0 16px 0;
    border-radius: 5px;
    box-sizing: border-box;
    opacity: 0;
    z-index: 555;
    box-shadow: 0px 20px 30px 0px rgba(28, 30, 35, 0.2), 0px -7px 40px 0px rgba(28, 30, 35, 0.2);
    visibility: hidden;
    text-align: left;
    text-transform: none;
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
    -moz-transform: translateY(10px);
    -ms-transform: translateY(10px);
    transition: opacity 200ms, transform 200ms;
    -webkit-transition: opacity 200ms, -webkit-transform 200ms;
}
.main-menu > ul > li > ul{
    margin-top: 18px;
}
.main-menu > ul > li:after{
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 18px;
}
.main-menu ul li:hover > ul{
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
}
.main-menu ul li ul ul.sub-menu{
    left: calc(100% + 6px);
    top: 0;
    margin-top: -17px;
}
.main-menu ul li ul.sub-menu li{
    display: block;
    float: none;
}
.main-menu a{
    transition: all 200ms;
    -moz-transition: all 200ms;
    -ms-transition: all 200ms;
    -webkit-transition: all 200ms;
}
.main-menu ul li ul li{
    & > a > span{
        position: relative;
        padding-bottom: 2px;
        &:after{
           content: '';
            position: absolute;
            bottom: 0;
            width: 100%;
            left: 0;
            opacity: 0;
            /* border-top: 1px solid; */
            transition: all 300ms;
        }
    }
    &:hover > a > span:after,
    &.current-menu-item > a > span:after{
       opacity: 1; 
    }
}
.main-menu ul li ul li.menu-item-has-children:after,
.main-menu > ul > li.menu-item-has-children > a:after{
    content: '';
    display: inline-block;
    border-left: 2px solid;
    border-top: 2px solid;
    width: 7px;
    height: 7px;
    position: absolute;
    bottom: 50%;
    bottom: calc(50% - 3px);
    right: 20px;
    transform: rotate(135deg);
}

.main-menu > ul > li.menu-item-has-children > a:after{
    position: static;
    display: inline-block;
    vertical-align: middle;
    opacity: 1;
    width: 6px;
    height: 6px;
    margin-left: 5px;
    z-index: 0;
    margin-top: -5px;
    transform: rotate(-135deg);
}
.main-menu ul li ul li.menu-item-has-children:hover:after{
    opacity: 1;
}
.main-menu ul li ul .menu-item{
    padding: 7px 25px 8px 28px;
}
.main-menu ul li ul .menu-item > a{
    color: inherit;
    opacity: 1;
    font-size: 14px;
    line-height: 1.6;
    width: 100%;
    display: block;
}

.main-menu ul li ul .menu-item > a:hover,
.main-menu ul li ul .menu-item.current-menu-item > a{
    opacity: 1;
}
.main-menu > ul > li:nth-last-child(1) > .sub-menu,
.main-menu > ul > li:nth-last-child(2) > .sub-menu,
.main-menu > ul > li:nth-last-child(3) > .sub-menu{
    left: auto;
    right: 0;
}
.main-menu > ul > li:nth-last-child(1) > .sub-menu .sub-menu,
.main-menu > ul > li:nth-last-child(2) > .sub-menu .sub-menu,
.main-menu > ul > li:nth-last-child(3) > .sub-menu .sub-menu{
    left: auto;
    right: calc(100% + 20px);
    &:before{
        left: auto;
        right: -9px;
        border-right: none;
        border-left: 9px solid #ffffff;
    }
}
.main-menu > ul > li > .sub-menu .sub-menu:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -20px;
    width: 20px;
}
.main-menu > ul > li:nth-last-child(1) > .sub-menu .sub-menu:after,
.main-menu > ul > li:nth-last-child(2) > .sub-menu .sub-menu:after,
.main-menu > ul > li:nth-last-child(3) > .sub-menu .sub-menu:after{
    left: auto;
    right: -20px;
}
.main_menu_container .menu_item_line{
    position: absolute;
    top: 0;
    height: 3px;
    transition: width .4s,left .6s cubic-bezier(0.21, 0.53, 0.01, 0.96);
}
.main-menu > ul > li > ul:before,
.main-menu > ul > li > ul:after,
.wpd_megamenu_triangle:before,
.wpd_megamenu_triangle:after{
    content: '';
    position: absolute;
    top: -8px;
    left: 20px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 8px solid #ffffff;
    z-index: 2;
}
.main-menu > ul > li > ul:before{
    box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 1);
}
.main-menu > ul > li > ul:after,
.wpd_megamenu_triangle:after{
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid rgba(232, 232, 232, 0.65);
    z-index: 0;
    top: -9px;
    margin-left: 0;
}
.main-menu > ul ul ul:before{
    content: '';
    display: block !important;
    position: absolute;
    top: 25px;
    left: -8px;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 8px solid #ffffff;
    z-index: 2;
}
.wpd_megamenu_triangle_container{
    display: block !important;
    width: 100% !important;
    height: 0 !important;
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
}
.wpd_megamenu_triangle{
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 1px;
    margin-left: -30px;
}
.main-menu > ul > li:nth-last-child(1) > .sub-menu:before, 
.main-menu > ul > li:nth-last-child(2) > .sub-menu:before, 
.main-menu > ul > li:nth-last-child(3) > .sub-menu:before, 
.main-menu > ul > li:nth-last-child(1) > .sub-menu:after, 
.main-menu > ul > li:nth-last-child(2) > .sub-menu:after, 
.main-menu > ul > li:nth-last-child(3) > .sub-menu:after{
    left: auto;
    right: 20px;
}
.left.header_side .main-menu > ul > li:nth-last-child(1) > .sub-menu:before, 
.left.header_side .main-menu > ul > li:nth-last-child(2) > .sub-menu:before, 
.left.header_side .main-menu > ul > li:nth-last-child(3) > .sub-menu:before, 
.left.header_side .main-menu > ul > li:nth-last-child(1) > .sub-menu:after, 
.left.header_side .main-menu > ul > li:nth-last-child(2) > .sub-menu:after, 
.left.header_side .main-menu > ul > li:nth-last-child(3) > .sub-menu:after{
    left: 20px;
    right: auto;
}
/* end menu style */
/* mega menu */
.wpd_header_builder__container .main-menu .wpd_megamenu_active > .sub-menu{
    width: auto;
    left: 50% !important;
    right: auto !important;
}
.wpd_header_builder__container .main-menu .wpd_megamenu_active > .sub-menu.huge_number_of_column{
