/* WPD Header Builder */
.wpd_header_builder{
    position: relative;
    z-index: 1000;
    font-weight: normal;
}
.wpd_header_builder a{
    color: inherit;
}
.wpd_header_builder.header_over_bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
}
.wpd_header_builder__section-container{ 
    position: relative;
    -js-display: flex;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-flow: row nowrap;
    -ms-flex-flow: row nowrap;
    flex-flow: row nowrap;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.wpd_header_builder__section-container:before,
.wpd_header_builder__section-container:after{
    display: none;
}
.wpd_header_builder__section-container.container_full{
    padding-left: 30px;
    padding-right: 30px;
}
.wpd_header_builder__section .header_side,
.wpd_header_builder__section.not_empty_center_side .header_side.center{
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    height: 100%;
    position: relative;
}
.wpd_header_builder__section .header_side.left,
.wpd_header_builder__section .header_side.header_side--custom-align.header_side--left-align{
    text-align: left;
}
.wpd_header_builder__section .header_side.right,
.wpd_header_builder__section .header_side.header_side--custom-align.header_side--right-align{
    text-align: right;
}
.wpd_header_builder__section .header_side.center,
.wpd_header_builder__section .header_side.header_side--custom-align.header_side--center-align{
    text-align: center;
}
.wpd_header_builder__section.not_empty_center_side .header_side,
.wpd_header_builder__section .header_side.header_side--custom-align,
.wpd_header_builder__section.not_empty_center_side .header_side.header_side--custom-align{
    -webkit-flex: 1 1 0;
    -ms-flex: 1 1 0;
    flex: 1 1 0;
}
.header_side_container{
    max-height: 100%;
    height: 100%;
    display: inline-block;
    white-space: nowrap;
}
.wpd_header_builder .header_side_container:before{
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}
.wpd_header_builder_component{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding-left: 5px;
    padding-right: 5px;
    z-index: 1;
    white-space: normal;
    &.wpd_header_builder_search_component{
        z-index: 2;
    }
}
.wpd_header_builder_component p,
.wpd_header_builder_component .button_size_small{
    margin-bottom: 0;
}
.wpd_header_builder_button_component a{
    background-color: transparent !important;
}
.wpd_header_builder__section .wpd_header_builder_button_component a:hover,
.wpd_header_builder__section .wpd_header_builder_button_component a:hover .wpd_btn_icon,
.sticky_header .wpd_header_builder__section--middle .wpd_header_builder_button_component a:hover,
.sticky_header .wpd_header_builder__section--middle .wpd_header_builder_button_component a:hover .wpd_btn_icon{
    color: #ffffff !important;
}
.wpd_header_builder_button_component a .wpd_btn_icon {
    color: inherit;
}
.wpd_header_builder .wpd_header_builder_cart_component > a{
    color: inherit;
}
.wpd_header_builder_delimiter_component{
    height: 15px;
    margin: 0 2px;
    width: 0;
    border-left: 1px solid;
    padding-left: 0;
    padding-right: 0;
    opacity: 0.2;
}
.wpd_header_builder_empty_space_component{
    width: 45px;
    padding: 0;
    &:last-child{
        width: 0;
    }
}
.wpd_header_builder__section--top .wpd_header_builder__section-container{
    z-index: 3;
}
.wpd_header_builder__section--middle .wpd_header_builder__section-container{
    z-index: 2;
}
.wpd_header_builder__section--bottom .wpd_header_builder__section-container{
    z-index: 1;
}
.wpd_header_builder > .mobile_menu_container .main-menu > ul:after{
    content: '';
    position: absolute;
    height: 1px;
    background: #f0f2f7;
    width: 100vw;
    bottom: 0;
    left: calc((-100vw + 100%)/2);
}
@media only screen and (max-width: 1200px) {
    .wpd_header_builder .sticky_header{
        display: none;
    }
    .wpd_header_builder_menu_component .main-menu{
        display: none;
    }
    .wpd_header_builder_menu_component .mobile-navigation-toggle{
        display: inline-block;
    }
    .wpd_header_builder > .mobile_menu_container .main-menu > ul{
        display: none;
    }
    .wpd_header_builder > .mobile_menu_container .main-menu{
        display: block;
    }
    .wpd_header_builder > .mobile_menu_container .wpd_header_builder_component{
        display: block;
    }
}
@media only screen and (max-width: 992px) {
    .wpd_header_builder__section--top{
        font-size: 11px;
    }
}
@media only screen and (max-width: 768px) {
    .wpd_header_builder__section.not_empty_center_side .header_side, 
    .wpd_header_builder__section .header_side.header_side--custom-align,
    .wpd_header_builder__section.not_empty_center_side .header_side.header_side--custom-align{
        -webkit-flex: 0 1 auto;
        -ms-flex: 0 1 auto;
        flex: 0 1 auto;
    }
    .wpd_header_builder__section--hide_on_mobile{
        display: none;
    }
    .wpd_header_builder.header_over_bg{
        position: relative;
    }
    .wpd_header_builder_button_component a.button_size_small{
        padding: 4px 8px;
        font-size: 12px;
    }   
    .wpd_header_builder_button_component .btn_icon_position_left .btn_icon_container{
        display: none;
    }
}

/* menu component */
.wpd_header_builder_component.wpd_header_builder_menu_component{
    /*height: 100%;*/
}
@media only screen and (max-width: 1200px) {
    .wpd_header_builder_empty_space_component{
        width: 12px;
    }
}
@media only screen and (max-width: 768px) {
    .wpd_header_builder_empty_space_component{
        width: 3px;
    }
    .logo_container:not(.logo_mobile_not_limited) img{
        height: auto!important;
    }
    .header_side_container .logo_container:not(.logo_mobile_not_limited){
        max-width: 90px;
        white-space: nowrap;
    }
}
/* end menu component */

/* search in header */
.header_search{
    display: inline-block;
    vertical-align: middle;
    height: auto !important;
}
.header_search__container{
    position: relative;
    z-index: 1;
    display: inline-block;
    height: 100%;
    width: 40px;
    line-height: 40px;
    text-align: center;
}
.header_search .header_search__icon{
    cursor: pointer;
    opacity: 1!important;
    /* margin-top: -2px; */
}
.header_search .header_search__icon > i{
    display: inline-block;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: FontAwesome;
    font-size: 1.1em;
    font-style: normal;
    font-weight: normal;
}
.header_search .header_search__icon > i:before{
    content: "\f002";
}
.header_search:before{
    content: '';
    position: absolute;
}
.header_search .header_search__inner{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    z-index: 1;    
    transition: all 200ms;
    -webkit-transition: all 200ms;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #222328;
        opacity: 0.5;
        transition: all .3s;
    }
    &:after{
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }
    .header_search__inner_cover{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0; 
        z-index: 1;       
    }
    .header_search__inner_close{
        position: absolute;
        top: 40px;
        right: 50px;
        width: 50px;
        height: 50px;
        cursor: pointer;
        z-index: 100;
        cursor: pointer;
        .header_search__search_close_icon{
            position: absolute;
            top: 15px;
            bottom: 15px;
            left: 50%;
            border-left: 2px solid #ffffff;
            transform: rotate(45deg);
            &:before{
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: -2px;
                border-left: 2px solid #ffffff; 
                transform: rotate(90deg);
            }
        }
    }
}
.header_search.ct-search-open .header_search__inner:before{
    opacity: 0.97;
}
.header_side.left .header_search .header_search__inner{
    right: auto;
    left: -11px;
}
body.ct-search-open .wpd_header_builder__section-container{
    z-index: 1 !important;
}
body.ct-search-open .sticky_header{
    z-index: 0;
}
body{
    /* transition: transform 0.5s; */
    transition: right .3s ease, left .3s ease ;
}
.header_search.ct-search-open .header_search__inner{ 
   transition: transform 0.5s;
}
.header_search.ct-search-open .header_search__inner{
    opacity: 1;
    visibility: visible;
}
.header_search__inner .search_form{
    padding-right: 40px;
    padding: 6vh 70px;
    position: relative;
    display: inline-block;
    z-index: 2;
    border: none;
    display: inline-flex;
    width: 98%;
    max-width: 768px;
}
.header_search__inner .search_form:after{
    content: '';
    position: absolute;
    bottom: 6vh;
    left: 70px;
    width: 0;
    height: 1px;
    border-top: 2px solid #ffffff;
    opacity: 0.1;
    transition: all 0.7s;
    transition-delay: 0.2s;
}
.header_search.ct-search-open .header_search__inner .search_form:after{
    width: calc(100% - 140px);
    opacity: 0.5;
}
.header_search__inner .search_form .search-submit{
    position: static;
    font-size: 22px;
    color: #ffffff;
    opacity: 0.4;
    width: 45px;
}
.header_search__inner .search_form .search-submit:hover{
    opacity: 0.8;
}
.header_search__inner .search-field{
    background-color: transparent;
    border: none;
    box-shadow: none;
    outline: none;
    user-select: none;
    height: 62px;
    padding: 0;
    margin-bottom: 0;
    font-size: 30px;
    color: #ffffff;
    font-weight: 900;
    width: 100%;
    border-radius: 5px;
    text-transform: capitalize;
}
.header_search__inner .search-field:-webkit-autofill,
.header_search__inner .search-field:-webkit-autofill:hover,
.header_search__inner .search-field:-webkit-autofill:focus{
    -webkit-box-shadow: 0 0 0 40px #222328 inset;
    border: 1px solid #222328;
    -webkit-text-fill-color: #ffffff;
    transition: background-color 5000s ease-in-out 0s;
}

.header_search__inner .search-field::-webkit-input-placeholder{
    opacity: 1 !important;
    color: #ffffff !important;
}
.header_search__inner .search-field:-moz-placeholder{
    opacity: 1 !important;
    color: #ffffff !important;
}
.header_search__inner .search-field::-moz-placeholder{
    opacity: 1 !important;
    color: #ffffff !important;
}
.header_search__inner .search-field:-ms-input-placeholder{
    opacity: 1 !important;
    color: #ffffff !important;
}
.top_header .header_search{
    height: auto !important;
}
@media only screen and (max-width: 768px) {
    .header_search__inner .search_form{
        padding: 6vh 35px;
    }
    .header_search.ct-search-open .header_search__inner .search_form:after{
        width: calc(100% - 70px);
    }
    .header_search__inner .search_form:after{
        left: 35px;
    }
}
/* end search in header */

/* wpml */
.wpd_header_builder_wpml_component .wpml-ls-legacy-dropdown,
.wpd_header_builder_wpml_component .wpml-ls-legacy-dropdown-click{
    width: auto;
    a{
        border: none;
        background: none;
    }
    a:hover,
    a:focus, 
    .wpml-ls-current-language:hover>a{
        color: inherit;
        background: transparent;
    }
    .wpml-ls-sub-menu{
        width: 12.5em;
        border-top: none;
        border-radius: 4px;
    }
}
.right.header_side .wpd_header_builder_wpml_component .wpml-ls-legacy-dropdown .wpml-ls-sub-menu,
.right.header_side .wpd_header_builder_wpml_component .wpml-ls-legacy-dropdown-click .wpml-ls-sub-menu{
    text-align: left;
}
.wpml-ls-legacy-dropdown a.wpml-ls-item-toggle:after{
    border: .25em solid transparent;
    border-top: .4em solid;
    position: absolute;
    right: 10px;
    top: calc(50% - .1em);
}
/* end wpml */

/* burger sidebar */
.wpd_header_builder_burger_sidebar_component{
    font-size: 0;
    line-height: 0;
    padding: 13px 8px;
    cursor: pointer;
}
.wpd_header_builder_burger_sidebar_component .burger_sidebar_icon{
    display: inline-block;
    width: 25px;
    height: 15px;
    position: relative;
}
.wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 0;
    border-top: 3px solid;
    transition: width 200ms;
    -moz-transition: width 200ms;
    -ms-transition: width 200ms;
    -webkit-transition: width 200ms;
}
.wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span.second{
    top: 6px;
    width: 17px;
}
.wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span.third{
    top: 12px;
    width: 21px;
}
.wpd_header_builder_burger_sidebar_component:hover .burger_sidebar_icon span{
    width: 100%;
}
.wpd_header_builder__burger_sidebar{
    position: fixed;
    z-index: 9999;
    top: 0;
    bottom: 0;
    right: -420px;
    width: 390px;
    max-width: calc(100% - 50px);
    background-color: #fff;
    overflow: hidden;
    -webkit-transition: right .3s ease-in-out, left .3s ease-in-out;
    -moz-transition: right .3s ease-in-out, left .3s ease-in-out;
    transition: right .3s ease-in-out, left .3s ease-in-out;
}
.wpd_header_builder__burger_sidebar.wpd_header_builder__burger_sidebar--left{
    right: auto;
    left: -420px;
}
.wpd_header_builder__burger_sidebar.active{
    right: 0;
}
.wpd_header_builder__burger_sidebar.wpd_header_builder__burger_sidebar--left.active{
    right: auto;
    left: 0;
}
.wpd_header_builder__burger_sidebar-cover{
    content: "";
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    visibility: hidden;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    -webkit-transition: opacity .3s ease, visibility .3s ease;
    -moz-transition: opacity .3s ease, visibility .3s ease;
    transition: opacity .3s ease, visibility .3s ease;
}
.wpd_header_builder__burger_sidebar.active .wpd_header_builder__burger_sidebar-cover{
    visibility: visible;
    opacity: 1; 
    cursor: url(../images/close.png), default;
}
body.admin-bar .wpd_header_builder__burger_sidebar{
    top: 32px;
}
.wpd_burger_sidebar_container{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 407px;
    padding: 40px 40px;
    box-sizing: border-box;
    overflow-y: scroll;
    background: #fff;
    z-index: 2;
}

@media only screen and (max-width: 1200px) {
    .wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span{
        width: 12px !important;
        left: 0;
        top: 2px;
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
    }
    .wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span:nth-child(2){
        display: none;
    }
    .wpd_header_builder_burger_sidebar_component .burger_sidebar_icon span:last-child{
        top: 9px;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
    }
}
@media only screen and (max-width: 768px) {
    .wpd_burger_sidebar_container{
        padding: 40px 25px;
        width: 100%;
    }
}

.wpd_burger_sidebar_container ul li{
    list-style: none;
}
body.active_burger_sidebar.burger_sidebar_right{
    right: 150px;
    left: auto;
}
body.active_burger_sidebar.burger_sidebar_left{
    right: auto;
    left: 150px;
}

/* login component */
.wpd_header_builder_login_component{
    line-height: 0;
    cursor: pointer;
    margin-bottom: 0;
    .wpd_login_icon{
        width: 30px;
        line-height: 30px;
        text-align: center;
        cursor: pointer;
        display: inline-block;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-family: FontAwesome;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        &.wpd_login_icon--avatar{
            border-radius: 30px;
            overflow: hidden;
            vertical-align: middle;
            &:before{
                display: none;
            }
            img.avatar{
                height: 30px;
            }
        }
        &:before{
            content: "\f090";
        }
        img.avatar,
        &.wpd_login_icon--avatar img.avatar{
            object-fit: cover;
        }
    }
    .wpd_login_button{
        display: inline-block;
        padding: 14px 15px;
        text-transform: uppercase;
        font-size: 11px;
        font-weight: 500;
        border: 2px solid;
        border-radius: 30px;
        transition: all 200ms;
    }
}
.wpd_login__user_name{
    position: absolute;
    left: 20px;
    top: 100%;
    display: inline-block;
    vertical-align: middle;
    font-weight: 500;
    -webkit-transition: all 200ms;
    transition: all 200ms;
    line-height: 1;
    padding: 5px 10px;
    background: #222328;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 5px;
    transform: translate(-50%, 10px);
    opacity: 0;
    visibility: hidden;
    &:before{
        content: '';
        position: absolute;
        top: -7px;
        left: 0;
        right: 0;
        height: 7px;
        opacity: 0;
    }
} 
.wpd_login_icon:hover + .wpd_login__user_name,
.wpd_login__user_name:hover{
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, 5px);
}
.wpd_login__logout{
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    padding: 7px 2px 7px 10px;
    position: relative;
    z-index: 1;
    i{
        font-size: 1.2em;
    }
    .wpd_login__logout_tooltip{
        position: absolute;
        left: 20px;
        top: 100%;
        display: inline-block;
        line-height: 1;
        padding: 5px 10px;
        background: #222328;
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: 5px;
        transform: translate(-50%, 10px);
        opacity: 0;
        visibility: hidden;
        font-weight: 500;
        transition: all 200ms;
        &:before{
            content: '';
            position: absolute;
            top: -7px;
            left: 0;
            right: 0;
            height: 7px;
            opacity: 0;
        }
    }
    &:hover .wpd_login__logout_tooltip{
        opacity: 1;
        visibility: visible;
        transform: translate(-50%, 5px);
    }
}
.wpd_header_builder__login-modal{
    position: fixed;
    width: 90%;
    height: 90%;
    z-index: 1000;
    top: 5%;
    left: 5%;
    text-align: center;
    box-sizing: border-box;
    opacity: 0;
    visibility: hidden;
    -webkit-transition:  opacity .3s ease-in-out;
    -moz-transition:  opacity .3s ease-in-out;
    transition:  opacity .3s ease-in-out;
    &.active{
        opacity: 1;
        visibility: visible;
    }
    &:before{
        content: '';
        display: inline-block;
        vertical-align: middle;
        position: relative;
        height: 100%;
    }
    .woocommerce-LostPassword{
        text-align: right;
        a{
            color: inherit;
            font-size: 13px;
            font-weight: 500;
            opacity: 0.5;
            text-decoration: underline;
            text-transform: capitalize;
            &:hover{
                opacity: 0.8;
            }
        }
    }
}
.wpd_woo_login_switcher__link{
    cursor: pointer;
    transition: all 300ms;
    -webkit-transition: all 300ms;
}
.wpd_woo_login_switcher__link:hover{
    color: inherit;
}
body .wpd_header_builder__login-modal form.login,
body .wpd_header_builder__login-modal form.register{
    padding: 0;
    border: none;
    background: transparent;
    margin: 0;
}
body .wpd_header_builder__login-modal #customer_login.col2-set .col-1, 
body .wpd_header_builder__login-modal #customer_login.col2-set .col-2{
    border-right: none;
    margin-bottom: 0;
}
body .wpd_header_builder__login-modal .woocommerce-form__label-for-checkbox{
    font-weight: 400;
    font-size: 14px;
}
body .wpd_header_builder__login-modal .woocommerce-form__label-for-checkbox span{
    vertical-align: middle;
    display: inline-block;
    margin-bottom: 0;
    font-size: 13px;
    font-weight: 500;
    text-transform: capitalize;
}
.wpd_header_builder__login-modal_container{
    display: inline-block;
    position: relative;
    vertical-align: middle;
    padding: 30px 40px 34px;
    box-sizing: border-box;
    background: #fff;
    z-index: 2;
    text-align: left;
    max-height: 100%;
    overflow-y: auto;
    border-radius: 5px;
    -box-shadow: 0 0 10px rgba(0,0,0,.4);
    transition:  all .3s ease-in-out;
    transform: translateY(60px);
}
.wpd_header_builder__login-modal.active .wpd_header_builder__login-modal_container{
    transform: translateY(0px);
