/* wpd_currency_switcher */
.wpd_currency_switcher{
    position: relative;
    display: inline-block;
    vertical-align: middle;
}
.wpml-ls-legacy-dropdown a{
    transition: all 0ms;
}
.wpd_currency_switcher:after{
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    height: 5px;
    width: 100%;
}
.wpd_currency_switcher ul{
    list-style: none;
    margin-top: 5px;
    display: block;
    position: absolute;
    left: 0;
    top: 100%;
    width: 160px;
    padding: 10px 0;
    border: 1px solid rgba(232, 232, 232, 0.8);
    border-top-color: rgba(232, 232, 232, 0.5);
    border-left-color: rgba(232, 232, 232, 0.5);
    border-radius: 4px;
    box-sizing: border-box;
    opacity: 0;
    z-index: 555;
    box-shadow: none;
    visibility: hidden;
    text-align: left;
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
    -moz-transform: translateY(10px);
    -ms-transform: translateY(10px);
    transition: all 200ms;
    -webkit-transition: all 200ms;
}
.wpd_currency_switcher ul:before, 
.wpd_currency_switcher ul:after {
    content: '';
    position: absolute;
    top: -10px;
    left: 20px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #ffffff;
    z-index: 2;
}
.wpd_currency_switcher ul:after {
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid rgba(232, 232, 232, 0.65);
    z-index: 0;
    top: -11px;
    margin-left: 0px;
}
.wpd_currency_switcher ul:before {
    -webkit-box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 1);
    -moz-box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 1);
}
.wpd_currency_switcher ul li{
    padding: 5px 25px 5px 20px;
    font-size: 0.87em;
    line-height: 1.6;
    font-weight: 400;
}
.wpd_currency_switcher:hover ul{
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
}
.wpd_currency_switcher a{
    position: relative;
    font-size: 12px;
    font-weight: 500;
}
.wpd_currency_switcher ul a:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    opacity: 0;
    border-top: 1px solid;
    transform: translateY(1px);
    -webkit-transform: translateY(1px);
    -moz-transform: translateY(1px);
    -ms-transform: translateY(1px);
    transition: all 300ms;
    -moz-transition: all 300ms;
    -ms-transition: all 300ms;
    -webkit-transition: all 300ms;
    will-change: transform;
}
.wpd_currency_switcher ul a:hover:after{
    opacity: 1;
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
}
.container_full .right.header_side .wpd_currency_switcher ul{
    left: auto;
    right: 0;
}
.container_full .right.header_side .wpd_currency_switcher ul:before,
.container_full .right.header_side .wpd_currency_switcher ul:after{
    left: auto;
    right: 20px;
}
select.woocommerce-currency-switcher,
select.currency-switcher{
    padding: 1px 12px;
    height: auto;
    border: none;
    background: none;
}
