/* ----------------------------------------------------------------------------
 * Custom Frontpage
 * ------------------------------------------------------------------------- */

.wpd_section_about{
    .wpd_about_text{
        font-size: 1.1428em;
        line-height: 187%;
    }
    .wpd_about_image__wrapper{
        .vc_box_rounded{
            
        }
    }
}
.wpd_section_services{
	background-color: #f7f8fa;
	.wpd_icon_box{
		padding-right: 35px;
		.wpd_icon_box__title h3{
			font-size: 16px;
			line-height: 1.4;
		}
	}
}
.wpd_section_call_to_action{
	text-align: center;
	background-position: center !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
	.wpd_call_to_action_title{
	    font-size: 60px;
	    line-height: 120%;
	    @media only screen and (max-width: 992px){
			& {
			    font-size: 40px;
    		}
		}
		@media only screen and (max-width: 768px){
			& {
			    font-size: 24px;
    		}
		}
	}
	.wpd_call_to_action_text{
		font-size: 20px;
		line-height: 180%;
		max-width: 67%;
		margin-left: auto;
		margin-right: auto;
		@media only screen and (max-width: 992px){
			& {
				font-size: 18px;
    		}
		}
		@media only screen and (max-width: 768px){
			& {
			    max-width: 87%;
    		}
		}
	}
	&.wpd_section_with_image{
		.wpd_call_to_action_title,
		.wpd_call_to_action_text{
			color: #ffffff;
		}
	}
}

.wpd_section_homepage_header{
	text-align: center;
	background-position: center !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    .wpd_homepage_header_title{
    	font-size: 100px;
    	line-height: 1.1;
    	@media only screen and (max-width: 992px){
			& {
				font-size: 80px;
    		}
		}
		@media only screen and (max-width: 768px){
			& {
				font-size: 35px;
    		}
		}
    }
    .wpd_homepage_header_text{
    	font-size: 18px;
    	line-height: 1.6;
    	@media only screen and (max-width: 992px){
			& {
				font-size: 16px;
    		}
		}
    }
    .wpd_homepage_header_pre_title .wpd_secondary_font{
    	font-size: 36px;
    	line-height: 1.39;
    	@media only screen and (max-width: 992px){
			& {
				font-size: 30px;
    		}
		}
		@media only screen and (max-width: 768px){
			& {
				font-size: 20px;
    		}
		}
    }
    .wpd_homepage_header_button_1,
    .wpd_homepage_header_button_2{
    	margin: 0 15px 20px;
    }
    &.wpd_section_with_image{
		.wpd_homepage_header_title,
		.wpd_homepage_header_text,
		.wpd_homepage_header_pre_title .wpd_secondary_font{
			color: #ffffff;
		}
	}
}

.site_wrapper.custom_frontpage{
	.main_wrapper{
		padding-top: 0;
	}
}

.wpd_section_testimonial{
	background-color: #e9e5df;
	.testimonials_photo{
		width: 50px;
	}
	.testimonials-text{
		font-size: 14px;
	}
}

.wpd_section_homepage_blog{
	.wpd_section_homepage_pre_title,
	.wpd_homepage_blog_title,
	.wpd_homepage_blog_text{
		text-align: center;
	}
}

.wpd_section_partners{
	background-color: #f7f8fa;
	.wpd_partners_item {
		max-width: 160px;
		text-align: center;
		margin-left: auto;
    	margin-right: auto;
	}
}

.wpd_section_contact{
	.wpd_contact_title_left,
	.wpd_contact_title_right{
		font-weight: 600;
	}
}