/**
 * Portfolio
 */
.wpdaddy_portfolio_list{
	& &__item{
		position: relative;
		z-index: 1;
		padding-bottom: 30px;
	}
	& &__item.span3{
		width: 25%;
	}
	& &__item--large_width_height.span3{
		width: 50%;
	}
	& &__item--large_width.span3{
		width: 50%;
	}
	@media only screen and (max-width: 768px){
		& &__item.span3{
			width: 100%;
		}
	}
	&__grid-sizer{
		height: 0 !important;
		min-height: 0 !important;
	}
	&__posts-container{
		.wpdaddy_portfolio_list__grid-gutter{
			width: 0;
		}
		&.no_spaces{
			margin-left: 0;
			margin-right: 0;
			.wpdaddy_portfolio_list__item{
				padding-left: 0;
				padding-right: 0;
				padding-bottom: 0;
			}
			.wpdaddy_portfolio_list__grid-gutter{
				width: 0;
			}
		}
		&.no_image_rounds{
			.wpdaddy_portfolio_list__item{
				.wpdaddy_portfolio_list__image-holder,
				.wpdaddy_portfolio_list__image-holder img,
				.wpdaddy_portfolio_list__image-holder > a:after,
				.wpdaddy_portfolio_list__image_placeholder{
					border-radius: 0;
				}
			}
		}
	}
	&__content_align--left{
		.wpdaddy_portfolio_list__content{
			text-align: left;
		}
	}
	&__content_align--center{
		.wpdaddy_portfolio_list__content{
			text-align: center;
		}
	}
	&__content_align--right{
		.wpdaddy_portfolio_list__content{
			text-align: right;
		}
	}
	&__filter{
		padding: 45px 0;
		margin-left: -18px;
		margin-right: -18px;
		font-weight: 600;
		text-transform: uppercase;
		a{
			display: inline-block;
			padding: 5px 18px;
		}
		&--left{
			text-align: left;
		}
		&--center{
			text-align: center;
		}
		&--right{
			text-align: right;
		}
	}
	&__image-holder{
		border-radius: 5px;
		overflow: hidden;
		transition: box-shadow 0.4s ease, opacity 0.4s ease;
		& > a{
			display: block;
			position: relative;
			z-index: 1;
			width: 100%;
			&:after{
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				z-index: 3;
				background-color: #27282c;
				border-radius: 5px;
				opacity: 0;
				transition: opacity 0.4s ease;
			}
		}
		img{
			width: 100%;
			max-width: none;
			border-radius: 5px;
			position: relative;
			z-index: 1;
			object-fit: cover;
			opacity: 0;
			transition: transform 0.4s ease, opacity 0.4s ease;			
		}
		&:hover > a:after{
			opacity: 0.75;
		}
		&:hover{
			box-shadow: 0px 30px 40px 0px rgba(34, 35, 40, 0.3);
		}
		&:hover img{
			transform: matrix(1.05, 0, 0, 1.05, -4.21534, -3.97493);
			transition: transform 2s ease;
		}
		.wpd_list__post_likes{
			position: absolute;
		    top: 20px;
		    right: 35px;
		    z-index: 1;
		    display: inline-block;
		    padding-left: 0;
		    color: #ffffff;
		    font-size: 17px;
		    opacity: 0;
		    transition: opacity 0.4s ease;
		    &.already_liked:hover{
		    	color: #ffffff;
		    }
		}
		&:hover .wpd_list__post_likes{
			opacity: 1;
		}
	}
	&__image-placeholder{
		background-color: #e2e2e2;
		border-radius: 5px;
  		position: relative;
  		top: 0;
		width: 100%;
		opacity: 1;
		.wpdaddy_portfolio_list__posts-container.no_image_rounds &{
			border-radius: 0;
		}
	}
	&__item.image_loaded &__image-holder img{
		opacity: 1;
	}
	&__content{
		padding-top: 22px;
		padding-bottom: 25px;
	}
	&__item{
		&.content_on_image .wpdaddy_portfolio_list__content{
			position: absolute;
			bottom: 30px;
			left: 40px;
			right: 40px;
			z-index: 2;
			opacity: 0;
			padding-top: 0;
			transition: opacity 0.4s ease;
			.wpdaddy_portfolio_list__title,
			.wpdaddy_portfolio_list__categories{
				color: #ffffff;
				
				a:hover{
					color: inherit;
				}
			}
			@media only screen and (max-width: 992px) and (min-width: 768px){
				& .wpdaddy_portfolio_list__title{
					font-size: 18px !important;
					line-height: 1.3;
				}
				& .wpdaddy_portfolio_list__categories{
					font-size: 14px !important;
					line-height: 1.3;
				}
			}
		}
		&.content_on_image .wpdaddy_portfolio_list__image-holder:hover .wpdaddy_portfolio_list__content{
			opacity: 1;
		}
	}	
	&__title{
		font-size: 21px;
		line-height: 1.5;
		font-weight: 500;
	}
	&__image_placeholder{
		position: relative;
		z-index: 1;
		width: 100%;
		padding-bottom: 80%;
		background-color: #e2e2e2;
		border-radius: 5px;
		.wpdaddy_portfolio_list__item--image_square &{
			padding-bottom: 100%;
		}
		.wpdaddy_portfolio_list__item--image_portred &{
			padding-bottom: 125%;
		}
	}
	&__categories{
		font-size: 14px;
		padding-top: 6px;
		a{
			color: inherit;
			font-weight: 400;
		}
	}
	&__posts-container.isotope_packery{
		.wpdaddy_portfolio_list__image-placeholder{
			margin-bottom: 0 !important;
		}
		.wpdaddy_portfolio_list__item--large_height .wpdaddy_portfolio_list__image-placeholder{
			padding-bottom: calc(200% + 30px) !important;
		}
		&.no_spaces .wpdaddy_portfolio_list__item--large_height .wpdaddy_portfolio_list__image-placeholder{
			padding-bottom: 200% !important;
		}		
		.wpdaddy_portfolio_list__image-holder img{
			position: absolute;
			top: 0;
			height: 100%;
		}
	}
}
.wpdaddy_portfolio_info{
	padding: 20px 0;
	display: flex;
	justify-content: space-between;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    a{
    	color: inherit;
    }
    &__wrapper{
    	margin-top: 20px;
    	margin-bottom: 35px;
    	border-top: 1px solid #f0f2f7;
		border-bottom: 1px solid #f0f2f7;
    }
	&__item{
		flex: 1 1 auto;
	    padding: 10px 10px;
	    min-width: calc(18% - 20px);
	    max-width: 33%;
	    font-weight: 400;
	    @media only screen and (max-width: 768px) {
	    	max-width: 50%;
	    }
	    &_title{
	    	font-size: 14px;
	    	line-height: 1.6;
	    	font-weight: 600;
	    	text-transform: uppercase;
	    	padding-bottom: 8px;
	    	&:after{
	    		content: ':';
	    	}
	    }
	}
	&__item_share{
		list-style: none;
		margin-left: -9px;
		margin-right: -9px;
		li{
			display: inline-block;
			padding: 0 9px;
			font-weight: 400;
			a{
				position: relative;
				z-index: 1;
				&:after{
					content: '';
					position: absolute;
					width: 100%;
					left: 0;
					bottom: -1px;
					border-bottom: 1px solid;
					opacity: 0;
					transition: all 400ms;
				}
				&:hover:after{
					opacity: 1;
				}
			}
			span:before{
				display: none;
			}
			.share_facebook{
				color: #3b5998;
			}
			.share_twitter{
				color: #55acee;
			}
			.share_gplus{
				color: #df4b38;
			}
			.share_pinterest{
				color: #bd081c;
			}
		}
	}
}
.wpdaddy_portfolio__footer{
	border-bottom: 1px solid #f0f2f7;
	display: table;
	width: 100%;
	padding-bottom: 20px;
	margin-top: 45px;
	@media only screen and (max-width: 768px) {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
	}
	.wpdaddy_portfolio_info__item_tag_wrapper,
	.wpdaddy_portfolio__like_and_comments{
		display: table-cell;
		vertical-align: middle;
		padding-bottom: 10px;
		@media only screen and (max-width: 768px) {
			display: block;
		}
	}
	.wpdaddy_portfolio_info__item_tag_wrapper{
		width: 100%;
		a{	
			display: inline-block;
			text-transform: uppercase;
			font-size: 11px;
			padding: 0px 10px;
			margin-right: 10px;
			margin-top: 5px;
			margin-bottom: 5px;
			border-radius: 5px;
			background-color: #f0f2f7;
			color: inherit;
			font-weight: 600;
			&:hover{
				color: #ffffff;
			}
		}
	}
	& + .comments-area{
		margin-top: 50px !important;
	}
}
.wpdaddy_portfolio_load_more{
	border-radius: 35px;
	white-space: nowrap;
}
.wpdaddy_single_portfolio_thumbnail{
	border-radius: 5px;
}
.wpdaddy_single_portfolio_thumbnail + .wpdaddy_portfolio_info__wrapper{
	margin-top: 10px;
	margin-bottom: 10px;
	border-top: none;
	border-bottom: none;
}
.wpdaddy_portfolio_single{
	.post-navigation{
		padding-top: 40px;
	}
}
.wpdaddy_portfolio_title{
	margin: 32px 0;
}
.wpd-page-title + .site_wrapper .wpdaddy_portfolio_single{
	padding-top: 0;
}