/**
 * Page Title
 */
.wpd-page-title{
	display: table;
    width: 100%;
	padding-top: 15px;
	padding-bottom: 15px;
    box-sizing: border-box;
    background-repeat: repeat;
    &__inner{
    	display: table-row;
		vertical-align: middle;
		&:before,
		&:after{
			content: '';
			display: table-cell;
		}
    }
    .container{
		display: table-cell;
		vertical-align: middle;
	}
	&.wpd-page-title_vert_align_top .container{
		vertical-align: top;
	}
	&.wpd-page-title_vert_align_bottom .container{
		vertical-align: bottom;
	}
	&__content{
		display: table;
		width: 100%;
		.page_title,
		.wpd_breadcrumb{
			display: table-cell;
			vertical-align: middle;
		}
		.page_title{
			width: 100%;
			h1{
				color: inherit;
			}
		}
	}
	&.wpd-page-title_horiz_align_center{
		.page_sub_title{
			font-size: 16px;
			line-height: 1.5;
			font-weight: 400;
			opacity: 0.5;
		}
		.page_title,
		.wpd_breadcrumb,
		.page_sub_title{
			display: table-row;
			text-align: center;
		}
		.page_title + .page_sub_title div{
			padding-top: 12px;
		}
		.page_sub_title + .wpd_breadcrumb > div{
			padding-top: 10px;
		}
	}
	.wpd_breadcrumb{
		font-weight: 500;
		a{
			color: inherit;
			opacity: 0.6;
			&:hover{
				opacity: 1;
			}
		}
		i{
			opacity: 0.6;
			font-weight: 700;
			margin: 0 5px;
		}
		.current{

		}
		.breadcrumbs{
			white-space: nowrap;
		}
	}	
	&.wpd-page-title_horiz_align_right .wpd-page-title__content{
		direction: rtl;
	}
	&.wpd-page-title_small_header {
		.wpd-page-title__content .page_title > h1{
		    font-size: 20px;
		    line-height: 1.5;
		}
		&.wpd-page-title_horiz_align_center .page_sub_title{
			font-size: 16px;
		}
		.wpd-page-title__content .wpd_breadcrumb{
			font-size: 13px;
		}
	}
}

@media only screen and (max-width: 768px) {
	.wpd-page-title{
		padding-top: 20px!important;
		background-attachment: scroll;
	}
	.wpd-page-title__content .page_title, 
	.wpd-page-title__content .wpd_breadcrumb{
		display: block;
	}
	.wpd-page-title__content .page_title + .wpd_breadcrumb{
		padding-top: 5px;
	}
	.wpd-page-title__content .page_title h1 {
	    font-size: 26px;
	    line-height: 140%;
	}
	.wpd-page-title__content .breadcrumbs{
		white-space: normal;
	}
}