/**
 * Grid SCSS
 */

.container,
#container {
	width:1170px;
	max-width: 100%;
	margin-right:auto;
	margin-left:auto;
}
.fullwidth-wrapper #container {
	width: auto;
}

.container:after,.container:before,
#container:after,#container:before, {
	display:table;
	line-height:0;
	content:"";
}

.container:after,
#container:after {
	clear:both;
}

.row {
	margin-left: -15px;
	margin-right: -15px;
}

.row:after,.row:before {
	display:table;
	line-height:0;
	content:"";
}

.row:after {
	clear:both;
}

.row [class*=span] {
	display:block;
	float:left;
	width:100%;
	min-height:1px;
	padding-right: 15px;
	padding-left: 15px;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box;
}

@media only screen and (min-width: 768px) {
	.row .span12 {
		width:100%;
	}

	.row .span11 {
		width: 91.66666666666666%;
	}

	.row .span10 {
		width: 83.33333333333334%;
	}

	.row .span9 {
		width: 75%;
	}

	.row .span8 {
		width: 66.66666666666666%;
	}

	.row .span7 {
		width: 58.333333333333336%;
	}

	.row .span6 {
		width: 50%;
	}

	.row .span5 {
		width: 41.66666666666667%;
	}

	.row .span4 {
		width: 33.33333333333333%;
	}

	.row .span3 {
		width: 25%;
	}

	.row .span1-5 {
		width: 20%;
	}

	.row .span2 {
		width: 16.666666666666664%;
	}

	.row .span1 {
		width: 8.333333333333332%;
	}
}

