/**
 * Widgets SCSS
 */

.widget {
  & + .widget{
    padding-top: 40px;
  }
  &-title {
	text-transform: uppercase;
    margin-bottom: 30px;
    font-weight: 600;
  }
}

/* Search Widget */
.search_form,
.woocommerce-product-search {
    position:relative;
    .search-submit,
    input[type="submit"],
    button[type="submit"] {
        display: block;
        position: absolute;
        right: 15px;
        top: 3px;
        line-height: 24px;
        font-size: 14px;
        z-index: 5;
        padding: 5px;
        margin: 0;
        border: none;
        background: none;
        opacity: 1;
        transition: opacity 300ms;
        &:hover {
            opacity: 0.5;
        }
    }
    .search-field {
        margin-bottom: 13px;
        padding-right: 50px;
        line-height: 21px;
    }
}

/* Menus & Lists Widgets */
.widget_product_categories ul,
.widget_nav_menu ul,
.widget_archive ul,
.widget_pages ul,
.widget_categories ul,
.widget_recent_entries ul,
.widget_meta ul,
.widget_recent_comments ul {
    padding:0;
    margin:-4px 0 0;
}

.widget_product_categories ul li,
.widget_nav_menu ul li,
.widget_archive ul li,
.widget_pages ul li,
.widget_categories ul li,
.widget_recent_entries ul li,
.widget_meta ul li,
.widget_recent_comments ul li {
    position:relative;
    padding:0 0 16px 16px;
    list-style:none;
    transition: color 300ms;
    font-weight: 600;
}

.widget_product_categories ul > li:last-child,
.widget_nav_menu ul > li:last-child,
.widget_archive ul > li:last-child,
.widget_pages ul > li:last-child,
.widget_categories ul > li:last-child,
.widget_recent_entries ul > li:last-child,
.widget_meta ul > li:last-child,
.widget_recent_comments ul > li:last-child {
    padding-bottom: 6px;
}

.widget_product_categories ul li ul,
.widget_nav_menu ul li ul,
.widget_archive ul li ul,
.widget_pages ul li ul,
.widget_categories ul li ul,
.widget_recent_entries ul li ul,
.widget_meta ul li ul,
.widget_recent_comments ul li ul {
  padding:12px 0 0 0px;
  margin:0;
}

.widget_product_categories ul li ul > li:last-child,
.widget_nav_menu ul li ul > li:last-child,
.widget_archive ul li ul > li:last-child,
.widget_pages ul li ul > li:last-child,
.widget_categories ul li ul > li:last-child,
.widget_recent_entries ul li ul > li:last-child,
.widget_meta ul li ul > li:last-child,
.widget_recent_comments ul li ul > li:last-child {
  padding-bottom: 0;
}

.widget_product_categories ul li:before,
.widget_nav_menu ul li:before,
.widget_archive ul li:before,
.widget_pages ul li:before,
.widget_categories ul li:before,
.widget_recent_entries ul li:before,
.widget_meta ul li:before,
.widget_recent_comments ul li:before {
    content:'';
    position:absolute;
    left:0;
    width:5px;
    height:5px;
    display:block;
    border-radius:50%;
    transition: all 300ms;
}

/* Featured Posts Widget */
.widget_posts ul.recent_posts {
  padding: 0;
  margin:0;
  display:block;
  list-style: none;
}

.widget_posts ul.recent_posts li {
  margin:0 0 23px 0;
  padding:0;
  position: relative;
  &.with_img {
      position: relative;
      min-height: 74px;
      padding-left: 92px;
      margin-bottom: 36px;
  }
}

.widget_posts ul.recent_posts li:before {
  display: none;
}

.widget_posts ul.recent_posts li:last-child {
  margin-bottom: 6px;
}

.widget_posts ul.recent_posts li.with_img:last-child {
  margin-bottom: 23px;
}

.recent_posts_img {
  width:80px;
  display: block;
  position: absolute;
  left: 0;
  top: 0px;
  & img {
    width:100%;
    height:auto;
    display:block;
    border-radius: 5px;
  }
}

.recent_post_meta {
  padding: 1px 0;
  font-size: 0.857em;
  a{
    color: inherit;
  }
}

.widget_posts .post_title,
.recent_post_meta {
  font-weight: 600;
}

/* Tag Clouds */
.tagcloud {
    font-size: 0;
    line-height: 0;
    margin: -10px 0 0 -12px;
    padding-bottom: 20px;
    & a {
        border: none;
        display:inline-block;
        margin: 12px 0 0 12px;
        font-size: 10px!important;
        line-height: 20px!important;
        border-radius: 4px;
        padding: 2px 10px;
        box-sizing: border-box;
        transition: all 300ms;
        text-transform: uppercase;
        font-weight: 600;
        background: #f0f2f7;
        &:hover {
            color: #ffffff !important;
        }
    }
}

/* Widget Calendar */
.widget_calendar {
    padding-bottom: 15px;
}
.calendar_wrap {
    border-radius: 5px;
    border: 1px #f0f2f7 solid;
    overflow: hidden;
    position: relative;
    & caption {
        font-size:0.875em;
        margin: 0;
        padding: 15px 40px;
        background: none;
        font-weight: 600;
        text-transform: uppercase;
    }
    & table,
    & td,
    & th {
        border-collapse: separate;
        padding: 0;
    }
    & table thead th {
        background: #f0f2f7;
        position: relative;
        border: 1px #e7ecf2 solid;
        border-left: 0;
        border-right: 0;
        padding: 12px 0 10px 0;
        font-size:0.875em;
    }
    & table tbody td {
        height: 43px;
        font-size: 0.9285em;
        font-weight: normal;
        & a {
            position: relative;
            z-index: 1;
            transition: all 300ms;
            &:before {
                background: #f0f2f7;
                display: block;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                content: '';
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
                border: 1px #e7ecf2 solid;
                transition: all 300ms;
            }
            &:hover {
                color: #fff;
            }
        }
        &#today {
            position: relative;
            color: #fff;
            z-index: 1;
            a{
               color: #fff; 
            }
            &:before {
                display: block;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                content: '';
                position: absolute;
                left: 50%;
                top: 50%;
                z-index: -1;
                transform: translate(-50%, -50%);
                border-width: 1px;
                border-style: solid;
                transition: all 300ms;
            }
        }
    }
    & table tfoot td {
        position: absolute;
        left: 12px;
        top: 15px;
        width: 30px;
        &.pad {
            display: none;
            visibility: hidden;
        }
        & a {
            display: block;
            height: 100%;
            width: 30px;
            font-size: 0;
            text-align: center;
            &:before {
                text-indent: 0;
                font-family: 'FontAwesome';
                font-weight:normal;
                content:"\f104";
                font-size: 18px;
            }
        }
        &#next {
            left: auto;
            right: 12px;
            & a:before {
                content:"\f105";
            }
        }
    }
}

/* Flickr Widget */
.widget_flickr {
    padding-bottom: 15px;
}

.flickr_widget_wrapper {
    display:block;
    font-size:0;
    line-height:0;
    margin:-5px 0 0 -7px;
}

.widget_flickr .flickr_badge_image {
    padding:7px 0 0 7px;
    width:25%;
    display:inline-block;
    position:relative;
    box-sizing:border-box;
    margin: 0;
    float: none;
}

.widget_flickr .flickr_badge_image a {
    display:block;
    width:100%;
    position:relative;
    overflow:hidden;
    transition:opacity 300ms;
}

.widget_flickr .flickr_badge_image a:hover {
    opacity:.7;
}

.widget_flickr .flickr_badge_image img {
    display:block;
    height:auto;
    width:100%;
    border-radius: 4px;
}

.flickr_badge_image .flickr_fadder {
    display:none;
}


/* RSS */
.widget_rss {
    & img {
        vertical-align: baseline;
        margin-right: 5px;
    }
    & ul {
        padding:0;
        margin:0;
        & li {
            list-style:none;
            padding-left:0;
            position:relative;
            padding-bottom:15px;
            margin-bottom:15px;
            &:before {
                display: none;
            }
            &:last-child {
                margin-bottom: 0;
            }
            & cite {
                text-align:right;
                display:block;
            }
            & .rss-date {
                display: block;
                font-size: 0.857em;
                padding: 1px 0;
            }
            & .rsswidget,
            & .rss-date {
                font-weight: normal;
            }
        }
    }
}

abbr[title] {
    text-decoration: none;
}

.about_info {
    text-align: center;
    & img {
        margin: 0 auto 35px auto;
        display: block;
        max-width: 150px;
        box-shadow: 0 20px 40px rgba(0,0,0, 0.4);
        &.rounded {
            border-radius: 50%;
            width: 150px;
            height: 150px;
        }
    }
    & p > a {
        display: inline-block;
        vertical-align: top;
        margin: 10px 10px 0 10px;
        &:hover {
            text-decoration: underline;
        }
        &.facebook,
        &.facebook:hover {
            color: #3b5998;
        }
        &.twitter,
        &.twitter:hover {
            color: #55acee;
        }
        &.google,
        &.google:hover {
            color: #df4b38;
        }
    }
}

/* Instagram */
.textwidget {
    & #sb_instagram {
        & #sbi_images {
            padding: 0 !important;
            margin: -5px 0 0 -7px;
            width: calc(100% + 7px);
            & .sbi_item {
                padding: 7px 0 0 7px !important;
                & .sbi_photo {
                    opacity: 1 !important;
                    & img {
                        border-radius: 4px;
                        transition: opacity 300ms;
                        opacity: 1 !important;
                        &:hover {
                            opacity:.7 !important;
                        }
                    }
                }
            }
        }
    }
}

.wpd_header_builder__burger_sidebar {
    .widget_nav_menu ul li{
        padding: 0;
        font-weight: inherit;
        a{      
        position: relative;
        display: block;
        z-index: 1;
        padding: 10px 0 10px 0;
        margin: 0 5px;
        }
        &:before{
            display: none;
        }
        ul{
            position: relative;
            top: 0;
            left: 0;
            width: 100%;
            padding: 0;
            padding-left: 15px;
            opacity: 1;
            visibility: visible;
            box-sizing: border-box;
            transition: all 0ms;
            background-color: transparent;
            transform: translateY(0);
            box-shadow: none;
            margin-top: 0;
            border: none;
            text-transform: none;
            li a{
                font-size: 14px;
                line-height: 1.6;
                width: 100%;
                display: block;
            }
        }
    } 
}
