/**
 * Button
 */
.wpd_module_button a {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 600;
    line-height: 19px;
    color: #fff;
    border-width: 1px;
    border-style: solid;
    &.wpd_button_rounded {
        border-radius: 50px;
    }
}
.wpd_module_button {
    position: relative;
    z-index: 20;
}
.wpd_module_button a{
   transition: all 400ms;
    -webkit-transition: all 400ms;
}
.wpd_module_button a:hover {
    background: #fff;
    border: 1px solid #e8e8e8;
}
.button_alignment_inline {
    display: inline-block;
    vertical-align: top;
}
.button_alignment_left {
    text-align: left;
}
.button_alignment_right {
    text-align: right;
}
.button_alignment_center {
    text-align: center;
}
.button_alignment_block a {
    display: block;
    text-align: center;
}
.button_size_mini {
    padding: 4px 12px;
}
.wpd_module_button a.button_size_mini{
    font-size: 12px;
    line-height: 21px;
}
.button_size_small {
    padding: 8px 20px;
}
.button_size_normal {
    padding: 13px 30px;
}
.button_size_large {
    padding: 18px 40px;
}
.wpd_btn_text,
.btn_icon_container {
    display: inline-block;
    vertical-align: middle;
}
.btn_icon_container img,
.btn_icon_container span {
    /*vertical-align: top;*/
}
.btn_icon_position_right .btn_icon_container {
    padding-left: 10px;
}
.btn_icon_position_left .btn_icon_container {
    padding-right: 10px;
}
.wpd_btn_icon.fa {
    line-height: 20px;
    font-size: 18px;
    color: #fff;
}

.wpd_btn_text {
    font-family: inherit;
    font-size: inherit;
    font-style: inherit;
    font-weight: inherit;
    color: inherit;
}