/**
 * Process
 */
.wpd_process_item{
	text-align:center;
	padding-top: 20px;
    padding-bottom: 20px;
	&:first-child{
		.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
			background-color: transparent !important;
		}
	}
	&:last-child{
		.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
			background-color: transparent !important;
		}
	}
	.wpd_process_item__number{
		padding-bottom: 5px;
	}
	.wpd_process_item__heading{
		padding-bottom: 7px;
		h1,h2,h3,h4,h5,h6{
			font-size: inherit;
			line-height: inherit;
			color: inherit;
		}
	}
	.wpd_process_item__circle_wrapp{
		display: inline-block;
		width: 100%;
		width: 100px;
		height: 100px;
		position: relative;
		&:before{
			content: '';
			display: inline-block;
			border: 10px solid;
			border-radius: 20px;
			position: absolute;
			top: 50%;
		    left: 50%;
		    margin-top: -10px;
		    margin-left: -10px;
		    box-shadow: 0 7px 20px 0 rgba(28, 30, 35, 0.4);
		    animation-name: pulse;
			animation-duration: 8s;
			animation-timing-function: linear;
			animation-iteration-count: infinite;
		}
		.wpd_process_item__circle_line_before,
		.wpd_process_item__circle_line_after{
			content: '';
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    width: 150px;
		    height: 2px;		
		    margin-top: -1px;    
		}
		.wpd_process_item__circle_line_before{
			margin-left: -150px;	
		}
		.wpd_process_item__circle{
			position: absolute;
		    top: 50%;
		    left: 50%;
		    margin-top: -50%;
		    margin-left: -50%;
		    width: 100%;
		    height: 100%;
		    border-radius: 200px;
		    border: 1px solid;
			animation-name: pulse;
			animation-duration: 8s;
			animation-timing-function: linear;
			animation-iteration-count: infinite;
			&.wpd_process_item__circle--size_mini{
				width: 80%;
				height: 80%;
				margin-top: -40%;
		    	margin-left: -40%;
			}
			&.wpd_process_item__circle--size_normal{
				width: 120%;
				height: 120%;
				margin-top: -60%;
		    	margin-left: -60%;
			}
			&.wpd_process_item__circle--size_large{
				width: 160%;
				height: 160%;
				margin-top: -80%;
		    	margin-left: -80%;
			}
			&.wpd_process_item__circle--size_e_large{
				width: 220%;
				height: 220%;
				margin-top: -110%;
		    	margin-left: -110%;
			}
		}
		@media only screen and (max-width: 992px) {
			&{
				width: 75px;
				height: 75px;
			}
		}
	}
	.wpd_process_item__number{
		font-size: 20px;
		font-weight: 600;
	}
	&.span6{
		.wpd_process_item__circle_wrapp{
			.wpd_process_item__circle_line_before,
			.wpd_process_item__circle_line_after{
				width: 300px;
			}
			.wpd_process_item__circle_line_before{
				margin-left: -300px;	
			}
		}
		&:nth-child(2n){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
				background-color: transparent !important;
			}
		}
		&:nth-child(2n+1){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
				background-color: transparent !important;
			}
		}
	}
	&.span4{
		.wpd_process_item__circle_wrapp{
			.wpd_process_item__circle_line_before,
			.wpd_process_item__circle_line_after{
				width: 200px;
			}
			.wpd_process_item__circle_line_before{
				margin-left: -200px;	
			}
		}
		&:nth-child(3n){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
				background-color: transparent !important;
			}
		}
		&:nth-child(3n+1){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
				background-color: transparent !important;
			}
		}
	}
	&.span3{
		&:nth-child(4n){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
				background-color: transparent !important;
			}
		}
		&:nth-child(4n+1){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
				background-color: transparent !important;
			}
		}
	}
	&.span1-5{
		.wpd_process_item__circle_wrapp{
			.wpd_process_item__circle_line_before,
			.wpd_process_item__circle_line_after{
				width: 120px;
			}
			.wpd_process_item__circle_line_before{
				margin-left: -120px;	
			}
		}
		&:nth-child(5n){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
				background-color: transparent !important;
			}
		}
		&:nth-child(5n+1){
			.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
				background-color: transparent !important;
			}
		}
	}
	@media only screen and (max-width: 768px) {
		.wpd_process_item__circle_wrapp{
			width: 100px;
			height: 100px;
			.wpd_process_item__circle_line_before,
			.wpd_process_item__circle_line_after{
				width: 200px !important;
			}
			.wpd_process_item__circle_line_before{
				margin-left: -200px !important;	
			}
		}
		.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_after{
			background-color: transparent !important;
		}
		.wpd_process_item__circle_wrapp .wpd_process_item__circle_line_before{
			background-color: transparent !important;
		}
	}
	&:nth-child(1){
		.wpd_process_item__circle,
		.wpd_process_item__circle_wrapp:before{
			animation-delay: 0s;
		}
	}
	&:nth-child(2){
		.wpd_process_item__circle,
		.wpd_process_item__circle_wrapp:before{
			animation-delay: 1.5s;
		}
	}
	&:nth-child(3){
		.wpd_process_item__circle,
		.wpd_process_item__circle_wrapp:before{
			animation-delay: 3s;
		}
	}
	&:nth-child(4){
		.wpd_process_item__circle,
		.wpd_process_item__circle_wrapp:before{
			animation-delay: 4.5s;
		}
	}
	&:nth-child(5){
		.wpd_process_item__circle,
		.wpd_process_item__circle_wrapp:before{
			animation-delay: 6s;
		}
	}
}

@media only screen and (max-width: 768px) {
	.wpd_process_item + .wpd_process_item{
		padding-top: 50px;
	}
}


@-webkit-keyframes pulse {
	0% {
	    -webkit-transform: scale(1);
	    transform: scale(1);
  	}
	5% {
		-webkit-transform: scale(1.2);
		transform: scale(1.2);
	}
	15% {
		-webkit-transform: scale(0.8);
		transform: scale(0.9);
	}
	20% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}

@keyframes pulse {
	0% {
	    -webkit-transform: scale(1);
	    transform: scale(1);
  	}
	5% {
		-webkit-transform: scale(1.2);
		transform: scale(1.2);
	}
	15% {
		-webkit-transform: scale(0.8);
		transform: scale(0.8);
	}
	20% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}